import React, { Component } from 'react';
import { TouchableOpacity, StyleSheet } from 'react-native';
import Ptext from './Ptext.js';
import Svg from 'react-native-svg'

class CheckBox extends Component {
  onPress = () => {
    if (this.props.onCheck)
      this.props.onCheck(!this.props.checked);
  }


  render () {
    return (
        <TouchableOpacity
                onPress={this.onPress}
                style={[styles.item,this.props.style]}
        >
          {
            this.props.checked ?
            <Svg width="24" height="25" viewBox="0 0 24 25" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path fill-rule="evenodd" clip-rule="evenodd" d="M4 6.7998C4 5.69524 4.89543 4.7998 6 4.7998H18C19.1046 4.7998 20 5.69524 20 6.7998V18.7998C20 19.9044 19.1046 20.7998 18 20.7998H6C4.89543 20.7998 4 19.9044 4 18.7998V6.7998Z" fill="#FFCB66"/>
              <path d="M6.59924 14.253C6.20872 13.8625 6.20872 13.2293 6.59924 12.8388C6.98977 12.4483 7.62293 12.4483 8.01346 12.8388L11.549 16.3743L10.8419 17.0814C10.4514 17.472 9.8182 17.472 9.42767 17.0814L6.59924 14.253Z" fill="black"/>
              <path d="M16.6704 8.76765C17.0609 8.37713 17.694 8.37713 18.0846 8.76765C18.4751 9.15818 18.4751 9.79134 18.0846 10.1819L11.0135 17.2529C10.623 17.6435 9.98982 17.6435 9.59929 17.2529L8.89219 16.5458L16.6704 8.76765Z" fill="black"/>
            </Svg>
            :
            <Svg width="16" height="17" viewBox="0 0 16 17" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path opacity="0.6" d="M0.5 2.7998C0.5 1.97138 1.17157 1.2998 2 1.2998H14C14.8284 1.2998 15.5 1.97138 15.5 2.7998V14.7998C15.5 15.6282 14.8284 16.2998 14 16.2998H2C1.17157 16.2998 0.5 15.6282 0.5 14.7998V2.7998Z" fill="white" stroke="#B4BBC2"/>
            </Svg>
          }
          <Ptext style={styles.title}>{this.props.label}</Ptext>
        </TouchableOpacity>
    )
   }
};

const styles = StyleSheet.create({
  button : {

  },
  title : {
    marginLeft : 13,
  },
  item : {
    flexDirection : 'row',
    justifyContent : 'flex-start',
    alignItems : 'center',
    minHeight : 30,
  }
});

export default CheckBox;
