import React, { Component } from 'react';
import { Image, StyleSheet,TouchableOpacity, ScrollView} from 'react-native';
import Htext from "../../common/Htext.js"

class StoriesItem extends Component{

  onPress = () => {
    if (this.props.onSelect)
      this.props.onSelect(this.props.id)
    console.log('Stories item ' + this.props.id + ' pressed');
  }

  render () {
    let item = {
      name : '',
      img_url : '',
    }
    if (this.props.item) {
      if (this.props.item.img_url)
        item.img_url = this.props.item.img_url;
      else
       item.img_url = require('../../icons/me-img-olga.png');
      if (this.props.item.name)
        item.name = this.props.item.name;
      else
        item.name = 'Пусто';
    }
    return (
      <TouchableOpacity onPress={this.onPress} style={{
            width : 120,
            height : 145,
            flexDirection : 'column',
            justifyContent:'center',
            alignItems : 'center'}}>
        <Image source={item.img_url} style={{width:88,height:88}}/>
        <Htext style={{marginTop : 18}}>{item.name}</Htext>
      </TouchableOpacity>
    )
  }
}


class Stories extends Component {

  render () {
    return (
      <ScrollView contentContainerStyle={[styles.mainContent]}
                style={{height : 145,
                  width : '100%',
                  shadowOpacity: 2,
                  shadowRadius: 2,
                  shadowColor: '#B4BBC2',
                  shadowOffset: { height: 0.5, width: 0 }}}
                  horizontal={true}>

        {
              this.props.items.map((prop,key,arr) => {
                return (
                    <StoriesItem key={key} id={key} item={prop} onSelect={this.props.onSelect}/>
                );
              })
        }

      </ScrollView>
    )
  }
}

const styles=StyleSheet.create({
  mainContent : {
    justifyContent: 'center',
    alignItems : 'center',
    flexDirection : 'column',
    backgroundColor : '#F1F3F5',
    width: '100%',
    flexWrap : 'wrap',
  }


});

export default Stories;
