import React, { Component } from 'react';
import { View, StyleSheet,TouchableOpacity } from 'react-native';
import Ptext from "../../common/Ptext.js"

class CommentsShort extends Component {


  render () {
    return (
      <TouchableOpacity style={styles.wrapper} onPress={this.props.openComments}>
        <View style={styles.wrapperHorizontal}>
          <View style={styles.wrapperVertComment}>
            <Ptext style={styles.username}>{this.props.comments[0].name}</Ptext>
            <Ptext style={styles.comment}>{this.props.comments[0].text}</Ptext>
          </View>
          <Ptext style={styles.time}>{this.props.comments[0].time}</Ptext>
        </View>
        <View style={styles.wrapperHorizontal}>
          <View style={styles.wrapperVertComment2ndLevel}>
            <Ptext style={styles.username}>{this.props.comments[0].reply[0].name}</Ptext>
            <Ptext style={styles.comment}>{this.props.comments[0].reply[0].text}</Ptext>
          </View>
          <Ptext style={styles.time}>{this.props.comments[0].reply[0].time}</Ptext>
        </View>
        <Ptext style={styles.allcomments}>Все комментарии({this.props.cnt})</Ptext>
      </TouchableOpacity>
    )
  }
}

const styles = StyleSheet.create({
  wrapper : {
    flexDirection : 'column',
    alignSelf : 'flex-start',
    alignItems : 'flex-start',
    justifyContent : 'flex-start',
    marginTop : 16,
  },
  wrapperVertical : {
    flexDirection : 'column',
    alignItems : 'flex-start',
    justifyContent : 'flex-start',
  },
  wrapperHorizontal : {
    flexDirection : 'row',
    alignItems : 'flex-start',
    justifyContent : 'flex-start',
    flex : 1,
  },
  wrapperVertComment : {
    flexDirection : 'column',
    alignItems : 'flex-start',
    justifyContent : 'flex-start',
    flex : 1,
  },
  wrapperVertComment2ndLevel : {
    flexDirection : 'column',
    alignItems : 'flex-start',
    justifyContent : 'flex-start',
    flex : 1,
    borderLeftWidth : 1,
    borderColor : '#B4BBC2',
    paddingLeft : 12,
  },
  username : {
    fontSize : 14,
    lineHeight : 16,
    textAlign : 'left',
    fontWeight : 'bold',
  },
  time : {
    fontSize : 14,
    lineHeight : 16,
    color : '#B4BBC2',
    textAlign : 'right',
  },
  comment : {
    fontSize : 14,
    lineHeight : 16,
    color : '#000',
    textAlign : 'left',
  },
  allcomments : {
    fontSize : 14,
    lineHeight : 16,
    color : '#B4BBC2',
    textAlign : 'left',
    alignSelf : 'flex-start',
    marginBottom : 18,
    marginTop : 18,
  }
});

export default CommentsShort;
