import React,{ Component } from 'react';
import { Image, View, StyleSheet, TouchableOpacity, TextInput } from 'react-native';
import Ptext from "../../common/Ptext.js"
import Button from "../../common/Button.js"
import RadioGroup from "../../common/RadioGroup.js"
import CheckBox from "../../common/CheckBox.js"



class ServiceExpertSelectStatus extends Component {
  render () {
    let full_name = this.props.item.firstName + ' ' + this.props.item.lastName;

    return (
      <View style={[expStyle.wrapper,this.props.style]}>
        <Image source={this.props.item.avatar} style={expStyle.avatar}/>
        <View style={expStyle.wrapperData}>
          <Ptext style={expStyle.nameText}>{full_name}</Ptext>
          {this.props.disabled ?
              <Button title={'Изменить'}
              style={expStyle.buttonStyle}
              onPress={this.props.onChangeExpert}/>
            :
              null}
        </View>
      </View>
    )
  }
}


class ServiceExpertDateSelectStatus extends Component {
  render () {
    return (
      <View style={[expStyle.wrapperDataDate]}>
        {!this.props.urgent ?
          <View style={[expStyle.wrapperDate,this.props.style]}>
              <Ptext style={expStyle.simpleText}>Консультация 22 декабря 2019 в 14:00</Ptext>
              <TouchableOpacity style={expStyle.edit} onPress={this.props.changeDate}>
                <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M13.4619 8.02218L15.903 10.4632L9.72394 16.6423L7.28425 14.2012L13.4619 8.02218ZM17.7553 7.43346L16.6666 6.34483C16.2459 5.92412 15.5628 5.92412 15.1406 6.34483L14.0978 7.38763L16.5389 9.8287L17.7553 8.61234C18.0816 8.28601 18.0816 7.75977 17.7553 7.43346ZM6.00679 17.6323C5.96237 17.8322 6.14288 18.0114 6.34283 17.9628L9.063 17.3032L6.62331 14.8622L6.00679 17.6323Z" fill="black"/>
                </svg>
              </TouchableOpacity>
          </View>
          :
          null
        }
          <CheckBox style={expStyle.checkbox} checked={this.props.urgent}
            onCheck={this.props.onChecked} label={'Срочный разбор в течении суток'}/>
      </View>
    )
  }
}

class ServiceEmailForm extends Component {
    render () {
      return (
        <View style={{flexDirection : 'column', width : '90%', alignSelf : 'center'}}>
          <Ptext style={{alignSelf : 'flex-start'}}>Укажите ваш E-mail:</Ptext>
          <View style={{flexDirection : 'row'}}>
            <TextInput style={expStyle.textinput} placeholder={'Email'}/>
            <Button style={{marginTop : 0, marginLeft : 15}} title={'Подтвердить'}
              onPress={this.props.reqConfirm}/>
          </View>
          {this.props.emailConfirm ?
          <TextInput style={expStyle.textinput} placeholder={'Код подтверждения'}/>
          :
          null
          }
        </View>
      )
    }
};

/*
<Ptext style={expStyle.checkbox}>Чек бокс срочного разбора</Ptext>

*/

const expStyle = StyleSheet.create({
  wrapper : {
    flexDirection : 'row',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
    backgroundColor : '#F1F3F5',
    alignSelf : 'center',
    width : '90%',
    borderRadius : 4,
  },
  wrapperDate : {
    flexDirection : 'row',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
    backgroundColor : '#F1F3F5',
    alignSelf : 'center',
    width : '100%',
    borderRadius : 4,
  },
  avatar : {
    width : 48,
    height : 48,
    marginTop : 14,
    marginLeft : 15,
  },
  nameText : {
    fontSize : 16,
    fontWeight : 'bold',
    lineHeight : 24,
  },
  wrapperData : {
    flexDirection : 'column',
    justifyContent : 'space-between',
    paddingTop : 14,
    paddingLeft : 14,
    height : 95,
  },
  wrapperDataDate : {
    flexDirection : 'column',
    justifyContent : 'space-between',
    marginTop : 14,
    alignSelf : 'center',
    height : 95,
    width : '90%',
    backgroundColor : '#ffffff',
  },
  buttonStyle : {
    minHeight : 32,
    height : 32,
    backgroundColor : '#F1F3F5',
    borderColor : '#d2d6da',
  },
  simpleText : {
    alignSelf : 'baseline',
    flex : 1,
    textAlign : 'left',
  },
  edit : {
    alignSelf : 'baseline'
  },
  checkbox : {
    alignSelf : 'flex-start',
  },
  textinput : {
    borderRadius : 4,
    borderWidth : 1,
    alignSelf : 'flex-start',
    borderColor : '#B4BBC2',
    minHeight : 40,
    padding : 8,
  }


});


class ServiceSelectExpert extends Component {
  constructor(props){
    super(props);

    this.state = {
      data : [{
        label : 'Выбрать автоматически',
        value : 'auto',
        selected : !this.props.manual,
      },
      {
        label : 'Выбрать вручную',
        value : 'manual',
        selected : this.props.manual,
      }
      ]
    };
  }

  onPress = data => {
    this.setState({ data });
  }

  render () {
    let manual = this.state.data[1].selected;
    console.log();
    return (
      <View style={styles.wholeForm}>
        <Ptext style={styles.title}>Консультацию и разбор выполнит:</Ptext>
        {!this.props.main ?
          <RadioGroup style={styles.radio}
            radioButtons={this.state.data}
            onPress={this.onPress} />
          :
          null
        }
        {!this.props.main ?
          <ServiceExpertSelectStatus item={this.props.experts[this.props.selectedId]}
            disabled={manual}
            onChangeExpert={this.props.onExpertChoose}
            />
          :
          <ServiceExpertSelectStatus item={this.props.expertMain[0]}
              disabled={false}
              />
        }
        <ServiceExpertDateSelectStatus
          changeDate={this.props.changeDate}
          urgent={this.props.urgent}
          onChecked={this.props.urgentCheck}
          />

        <ServiceEmailForm
          reqConfirm={this.props.reqConfirm}
          emailConfirm={this.props.emailConfirm}/>

        <Button style={styles.button}
          title={'Перейти к оплате'}
          onPress={this.props.onNextStage}
          />
      </View>

    )
  }
}

const styles = StyleSheet.create({
  wholeForm : {
    flexDirection : 'column',
    width : '100%',
    flex : 1,
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
    alignSelf : 'center',
    backgroundColor : '#ffffff',
    paddingBottom : 36,
  },
  radio : {
    marginLeft : 15,
  },
  title : {
    fontSize : 16,
    marginTop : 12,
    paddingLeft : 15,
    fontWeight : 'bold',
    lineHeight : 24,
  },
  caption : {
    textAlign : 'left',
    marginTop : 8,
    marginLeft : 16,
    marginBottom : 4,
    fontSize : 14,
  },
  oneItem : {
    flexDirection : 'column',
    width : '100%',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
  },
  comment : {
    width : '90%',
    borderRadius : 4,
    borderColor : '#B4BBC2',
    height : 120,
    borderWidth : 1,
    padding : 8,
    alignSelf : 'center'
  },
  button : {
    width : 208,
    marginRight : 16,
    marginLeft : 16,
    alignSelf : 'flex-start',
    height : 40,
    borderWidth : 0,
  }

});


export default ServiceSelectExpert;
