import React, { Component } from 'react';
import { TouchableOpacity, StyleSheet, View } from 'react-native';
import Ptext from './Ptext.js';
import Svg from 'react-native-svg'

class RadioGroup extends Component {
  onPress = (key) => {
    let c = this.props.radioButtons;
    c.map((item,key,arr)=> {
      item.selected = false;
      return 0;
    }
    );
    c[key].selected = true;
    this.props.onPress(c);

  }


  render () {
    return (
      <View style={this.props.style}>
      {this.props.radioButtons.map((item,key,arr) => {
        return (
          <TouchableOpacity key={key}
                onPress={this.onPress.bind(this,key)}
                style={styles.item}
                disabled={item.disabled}>
          {
            item.selected ?
            <Svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path fill-rule="evenodd" clip-rule="evenodd" d="M8 16C12.4183 16 16 12.4183 16 8C16 3.58172 12.4183 0 8 0C3.58172 0 0 3.58172 0 8C0 12.4183 3.58172 16 8 16Z" fill="#FFCB66"/>
            <path opacity="0.1" d="M15.5 8C15.5 12.1421 12.1421 15.5 8 15.5C3.85786 15.5 0.5 12.1421 0.5 8C0.5 3.85786 3.85786 0.5 8 0.5C12.1421 0.5 15.5 3.85786 15.5 8Z" stroke="black"/>
            <path fill-rule="evenodd" clip-rule="evenodd" d="M8 12C10.2091 12 12 10.2091 12 8C12 5.79086 10.2091 4 8 4C5.79086 4 4 5.79086 4 8C4 10.2091 5.79086 12 8 12Z" fill="black"/>
            </Svg>
            :
            <Svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
            {
              item.disabled ? <path opacity="0.2" fill-rule="evenodd" clip-rule="evenodd" d="M8 16C12.4183 16 16 12.4183 16 8C16 3.58172 12.4183 0 8 0C3.58172 0 0 3.58172 0 8C0 12.4183 3.58172 16 8 16Z" fill="#B4BBC2"/>
                : null
            }
            <path opacity="0.6" d="M15.5 8C15.5 12.1421 12.1421 15.5 8 15.5C3.85786 15.5 0.5 12.1421 0.5 8C0.5 3.85786 3.85786 0.5 8 0.5C12.1421 0.5 15.5 3.85786 15.5 8Z" stroke="#B4BBC2"/>
            </Svg>

          }
          <Ptext style={styles.title}>{item.label}</Ptext>
          </TouchableOpacity>


        )
      })

      }
       </View>
    )
   }
};

const styles = StyleSheet.create({
  button : {

  },
  title : {
    marginLeft : 13,
  },
  item : {
    flexDirection : 'row',
    justifyContent : 'flex-start',
    alignItems : 'center',
    minHeight : 30,
  }
});

export default RadioGroup;
