import React, { Component } from 'react';
import { View, Image, StyleSheet,TouchableOpacity } from 'react-native';
import Svg from 'react-native-svg';
import Button from "../../common/Button.js"
import Ptext from "../../common/Ptext.js"
import Htext from "../../common/Htext.js"

/*

<Button style={styles.controlsAsk}
  onPress={this.props.onAskQuestion}
  title={'Задать вопрос'}/>

*/

class ExpertExtInfo extends Component {
  constructor(props){
    super(props);
    this.state = {
      showInfo : false,
    }
  }

  changeMenuSelected = () => {
    this.setState({showInfo : !this.state.showInfo});
    console.log("State is " + this.state.showInfo);
  }


  render () {
    let full_name = this.props.info.firstName + ' ' + this.props.info.lastName;
    let experience = 'Опыт ' + this.props.info.experience + ' года';
    let sinceText = 'Запись ' + this.props.info.freeSince;
    /*let color = '#9aeb71';
    if (this.props.info.freeSince !== undefined)
      color = '#FFEECC';
*/
    let select_about = {
      borderBottomWidth : 2,
      borderColor : '#45B4E3',
    };
    let select_review = {
      borderBottomWidth : 2,
      borderColor : '#45B4E3',
    };
    if (!this.state.showInfo)
      select_review.borderBottomWidth = 0;
    else
      select_about.borderBottomWidth = 0;




    return (

      <View style={styles.wrapper}>
        <View style={styles.mainHeader}>
          <TouchableOpacity onPress={this.props.onCbBack}>
          <Svg style={{margin : 8}} width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path fill-rule="evenodd" clip-rule="evenodd" d="M15.4 7.4L14 6L8 12L14 18L15.4 16.6L10.8 12L15.4 7.4Z" fill="black"/>
          </Svg>
          </TouchableOpacity>
          <Htext style={styles.mainHeaderTitle}>{full_name}</Htext>
        </View>


        <View style={styles.header}>
          <Image style={styles.headerAvatar} source={this.props.info.avatar}/>
          <View style={styles.headerInfo}>
            <Ptext>{experience}</Ptext>
            <Ptext>Заказов выполнено: {this.props.info.ordersCnt}</Ptext>
            <Ptext style={styles.freeSince}>{this.props.info.freeSince === undefined ? 'Свободна' : sinceText}</Ptext>
          </View>
        </View>

        <View style={styles.controls}>
          <Button style={styles.controlChoose}
            onPress={this.props.onSelect} title={'Выбрать'}/>
        </View>

        <View style={styles.menuSelect}>
          <TouchableOpacity style={[styles.menuItem,select_about]}
            onPress={this.changeMenuSelected}>
            <Ptext style={styles.menuCaption}>Обо мне</Ptext>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.menuItem,select_review]}
            onPress={this.changeMenuSelected}>
            <Ptext style={styles.menuCaption}>Отзывы({this.props.info.reviewCnt})</Ptext>
          </TouchableOpacity>
        </View>
        {!this.state.showInfo ?
        <Ptext style={styles.descr}>
          {this.props.info.extdescr}
        </Ptext>
        :
        <Ptext style={styles.descr}>
          {this.props.info.review}
        </Ptext>
        }

        <View style={styles.controls}>
          <Button style={styles.controlChoose}
            onPress={this.props.onSelect} title={'Выбрать'}/>
        </View>

      </View>
    )
  }
}

const styles = StyleSheet.create({
  wrapper : {
    flexDirection : 'column',
    width : '90%',
    backgroundColor : '#fffff',
  },
  mainHeader : {
    flexDirection : 'row',
    justifyContent : 'flex-start',
    alignItems : 'center',
    marginTop : 20,
  },
  mainHeaderTitle : {
    fontSize : 20,
  },
  header : {
    flexDirection : 'row',
    justifyContent : 'flex-start',
    alignItems : 'flex-start'
  },
  headerAvatar : {
    width : 64,
    height : 64,
    margin : 12
  },
  headerInfo : {
    flexDirection : 'column',
    justifyContent : 'space-between',
    alignItems : 'flex-start',
    marginTop : 24,
  },
  freeSince : {
    fontWeight : 'bold',
  },
  orders : {
    flexDirection : 'row',
    justifyContent : 'flex-start',
    alignItems : 'center',
    marginBottom : 6,
  },
  ordersIcon : {
    marginLeft : 12,
    marginRight : 12,
  },
  ordersCaption : {

  },
  menuSelect : {
      flexDirection : 'row',
      justifyContent : 'flex-start',
      alignItems : 'center'
  },

  menuCaption : {
    margin : 12,
  },

  descr : {
    margin : 12,
    textAlign : 'left',
  },
  controls : {
    flexDirection : 'row',
    justifyContent : 'flex-start',
    flexWrap : 'wrap',
  },
  controlChoose : {
    minWidth : 236,
    marginLeft : 12,
    flex : 1,
    width : '100%',
  },
  controlsAsk : {
    backgroundColor : '#ffffff',
    marginLeft : 12,
    minWidth : 235,
    borderWidth  : 1,
    flex : 1,
    width : '100%',
  },
});

export default ExpertExtInfo;
