import React, { Component } from 'react';
import { View, StyleSheet, TextInput, TouchableOpacity } from 'react-native';
import Svg from 'react-native-svg';
import Ptext from "../../../common/Ptext.js"
import Button from "../../../common/Button.js"

class ContactListHeader extends Component {
  constructor(props){
    super(props);
    this.state = {
      search : '',
      actButton : 0,
    };
  }

  _changeText = (text) => {
    this.setState({search : text});
    if (this.props.onSearchText)
      this.props.onSearchText(text);
  }
  _onPress = () => {
    this.refs.SearchInputField.focus();
  }

  _buttonSelect = (id) => {
    this.setState({actButton : id});
    if (this.props.selectChange)
      this.props.selectChange(3^id);
  }

  render () {
    let button_active = {
      backgroundColor : '#FFCB66',
      borderColor : '#FFCB66',
    };
    let button_set = [{},{},{}];
    button_set[this.state.actButton] = button_active;
    return (
      <View style={styles.verticalWrapper}>
        {/* Chat label, search symbol and add button*/}
        <View style={styles.horizontalWrapper}>
          <Ptext style={styles.label}>Чаты</Ptext>
          <TouchableOpacity onPress={this._onPress} style={styles.search}>
              <TextInput onChangeText={this._changeText} style={{flex : 2}}
                ref='SearchInputField'/>
              <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M9.5 3C11.2239 3 12.8772 3.68482 14.0962 4.90381C15.3152 6.12279 16 7.77609 16 9.5C16 11.11 15.41 12.59 14.44 13.73L14.71 14H15.5L20.5 19L19 20.5L14 15.5V14.71L13.73 14.44C12.59 15.41 11.11 16 9.5 16C7.77609 16 6.12279 15.3152 4.90381 14.0962C3.68482 12.8772 3 11.2239 3 9.5C3 7.77609 3.68482 6.12279 4.90381 4.90381C6.12279 3.68482 7.77609 3 9.5 3ZM9.5 5C7 5 5 7 5 9.5C5 12 7 14 9.5 14C12 14 14 12 14 9.5C14 7 12 5 9.5 5Z" fill="#B4BBC2"/>
              </Svg>
          </TouchableOpacity>
          <TouchableOpacity style={styles.buttonPlus} onPress={this.props.showCreateChat}>
            <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <rect x="11" y="6" width="2" height="12" rx="1" fill="#B4BBC2"/>
            <rect x="6" y="13" width="2" height="12" rx="1" transform="rotate(-90 6 13)" fill="#B4BBC2"/>
            </Svg>
          </TouchableOpacity>
        </View>
        {/* Three state button`s selector*/}
        <View style={styles.horizontalWrapper}>
            <Button title='Все' style={[styles.buttonLeft,button_set[0]]} styleTitle={styles.buttonStyleTitle}
              onPress={this._buttonSelect.bind(this,0)}/>
            <Button title='Группы' style={[styles.buttonMiddle,button_set[1]]} styleTitle={styles.buttonStyleTitle}
              onPress={this._buttonSelect.bind(this,1)}/>
            <Button title='Личные' style={[styles.buttonRight,button_set[2]]} styleTitle={styles.buttonStyleTitle}
              onPress={this._buttonSelect.bind(this,2)}/>
        </View>
      </View>
    )
  }
}

const styles = StyleSheet.create({
  verticalWrapper : {
    flexDirection : 'column',
    width : '90%',
    alignItems : 'center',
    marginTop : 15,
    justifyContent : 'flex-start',
    alignSelf : 'center',
    backgroundColor : '#fff',
  },
  horizontalWrapper : {
    flexDirection : 'row',
    width : '100%',
    justifyContent : 'flex-start',
    alignSelf : 'center',
  },
  label : {
    fontSize : 24,
    lineHeight : 32,
    fontWeight : 'bold',
  },
  search : {
    flex : 3,
    height : 32,
    justifyContent : 'center',
    alignItems : 'center',
    flexDirection : 'row',
    marginLeft : 6,
  },
  buttonPlus : {
    width : 32,
    height : 32,
    borderWidth : 1,
    borderRadius : 4,
    borderColor : '#B4BBC2',
    justifyContent : 'center',
    alignItems : 'center',
    marginLeft : 6,
  },
  buttonLeft : {
    minWidth : 96,
    borderTopRightRadius : 0,
    borderBottomRightRadius : 0,
    backgroundColor : '#B4BBC2',
    borderColor : '#B4BBC2',
    alignSelf : 'center',
    flex : 1,
  },
  buttonMiddle : {
    minWidth : 96,
    borderRadius : 0,
    backgroundColor : '#B4BBC2',
    borderColor : '#B4BBC2',
    alignSelf : 'center',
    flex : 1,
  },
  buttonRight : {
    minWidth : 96,
    borderTopLeftRadius : 0,
    borderBottomLeftRadius : 0,
    backgroundColor : '#B4BBC2',
    borderColor : '#B4BBC2',
    alignSelf : 'center',
    flex : 1,
  },
  buttonStyleTitle : {
    fontSize : 14,
    lineHeight : 20,
  }
});

export default ContactListHeader;
