import React, { Component } from 'react';
import { View, StyleSheet } from 'react-native';
import { Dimensions } from 'react-native';

import ItemHeaderLabel from "./Formitems/headerlabel.js";
import ItemFavouritsIcon from "./Formitems/favouritsicon.js";
import ItemDateInput from "../../common/dateinput.js";
import ItemGenderSelector from "../../common/genderselector.js";
import ItemCaculateButton from "./Formitems/calculatebutton.js";


class CalculateForm extends Component
{

    constructor(props){
      super(props);
      this.state = {
        dims : {}
      };
      this.dims_changed({window : Dimensions.get("window")});
    }

    componentWillMount() {
          Dimensions.addEventListener("change", this.dims_changed);
    }

    componentWillUnmount() {
      // Important to stop updating state after unmount
        Dimensions.removeEventListener("change", this.dims_changed);
    }

    dims_changed = dims => {
      let res = this.props.supp.testRange(this.state.dims.width,dims.window.width);
      if (res.neq) {
          let s = this.state;
          switch (res.range) {
            case 0:
                  s.label = '80%';
                  s.favourits = '10%';
                  s.bidInput = '40%';
                  s.genderSwitch = '40%';
                  s.calcButton = '100%';
                  s.favourits_top = true
              break;
            case 1:
                  s.label = '80%';
                  s.favourits = '10%';
                  s.bidInput = '20%';
                  s.genderSwitch = '26%';
                  s.calcButton = '40%';
                  s.favourits_top = true
              break;
              case 2:
              case 3:
              case 4:
                  s.label = '14%';
                  s.favourits = '5%';
                  s.bidInput = '15%';
                  s.genderSwitch = '30%';
                  s.calcButton = '20%';
                  s.favourits_top = false
                break;
              default:
                    s.label = '14%';
                    s.favourits = '5%';
                    s.bidInput = '15%';
                    s.genderSwitch = '30%';
                    s.calcButton = '20%';
                    s.favourits_top = false
                break;
          }
          s.dims = dims.window;
          this.setState(s);
      }
    }
/*
<ItemGenderSelector style={{width : this.state.genderSwitch}} onChange={this.props.cb.cbGenderInput}/>

*/


    render () {

      return (
        <View style={styles.form}>
          <ItemHeaderLabel style={{width : this.state.label}}/>
          {this.state.favourits_top ? <ItemFavouritsIcon style={{width : this.state.favourits}} onPress={this.props.openFav}/> : null }
          <ItemDateInput style={{width : this.state.bidInput}} onChangeText={this.props.cb.cbDateInput} bidValue={this.props.bidValue}/>
          <ItemCaculateButton style={{width : this.state.calcButton, minWidth : 147}} onPress={this.props.cb.cbCalcPress}/>
          {!this.state.favourits_top ? <ItemFavouritsIcon style={{width : this.state.favourits}} onPress={this.props.openFav}/> : null }
        </View>
      )
    }
/*


*/
}

const styles = StyleSheet.create({
  form : {
    flexDirection : 'row',
    backgroundColor: '#fff',
    alignItems : 'center',
    alignContent: 'center',
    justifyContent : 'space-between',
    width : '90%',
    flexWrap : 'wrap',
    margin : 16,
    padding : 16,
    borderRadius : 4,
    shadowOpacity: 0.2,
    shadowRadius: 2,
    shadowColor: '#000',
    shadowOffset: { height: 1, width: 0 },
  }

})
export default CalculateForm
