import React,{Component} from 'react';
import { StyleSheet, ScrollView } from 'react-native';
import BlogListItem from './blogListItem.js';
import BlogPostStageHeader from './BlogPostStageHeader.js';


class BlogPostPage extends Component {


  render () {
    let whole_width = '100%';
    if (this.props.scrollWidth)
      if (this.props.scrollWidth > 720 )
       whole_width = '95%';

    return(
      <ScrollView style={[styles.wrapper,{width:whole_width, height : this.props.t_height}]}
        contentContainerStyle = {{
          justifyContent : 'flex-start',
          alignItems : 'flex-start'}}>
        <BlogPostStageHeader stage={1} onBackPressed={this.props.closePage}/>
        <BlogListItem scrollWidth={this.props.scrollWidth}
          openComments={this.props.openComments}
          post={this.props.post} id={this.props.id}
          trim={false}/>
      </ScrollView>
    )
  }
}

const styles = StyleSheet.create({
  wrapper : {
    flexDirection : 'column',
    alignSelf : 'center',

  },
});

export default BlogPostPage;
