import React,{ Component } from 'react'
import AuthForm from "../../components/login/loginPage.js"
import RegisterPage from "../../components/register/registerPage.js"
import Modal from 'modal-react-native-web';

class LoginPageContainer extends Component {
  constructor(props){
    super(props);
    this.state = {};
    this.state.user = {};
    this.state.auth = false;
    this.state.showreg = false;
    this.state.form = {
      privacy_policy : null,
      terms_of_service : null,
    };
    this.state.captcha = {};
  }

  passInput = (value) => {
    let s = this.state;
    s.user.pass = value;
    s.form.password = value;
    this.setState(s);
  }
  emailInput = (value) => {
    let s = this.state;
    s.form.email = value;
    s.user.email = value;
    this.setState(s);
  }

  captchaInput = (value) => {
    let s = this.state;
    s.form.captcha = value;
    this.setState(s);
  }
  userInput = (value) => {
    let s = this.state;
    s.form.username = value;
    this.setState(s);
  }

  loginPress = () => {
    this.props.ajax.post(true,'http://numerolog.privatebot.info:8080/forum/api/auth/',{
      username : this.state.user.email,
      password : this.state.user.pass,
    }).then(
        (data) => {
          this.props.setUserInfo(data);
          this.props.cbClose(true);
        }
    )
    .catch(
      (rejection) => {
        console.log('Failed login returned rejection cause');
        alert(rejection.detail);
      }
    )
  }


  registerPress = () => {
    let s = this.state;
    this.renewCaptchaPress();
    s.showreg = true;
    this.setState(s);
  }

  toLoginPage = () => {
    let s = this.state;
    console.log('to login page');
    s.showreg = false;
    this.setState(s);
  }

  renewCaptchaPress = () => {
    this.props.ajax.get(false,'http://numerolog.privatebot.info:8080/forum/api/captcha-question/',{})
    .then(
        (data) => {
          let s = this.state;
          console.log(data);
          s.captcha = data;
          this.setState(s);
          }
    )
    .catch(
      (rejection) => {
        console.log('Failed renew captcha rejection cause');
        let s = this.state;
        s.captcha = {};
        console.log(rejection);
        this.setState(s);
      }
    )
  }

  registerSubmitPress = () => {
    this.props.ajax.post(true,'http://numerolog.privatebot.info:8080/forum/api/users/',this.state.form,null)
    .then(
      (data) => {
        console.log("user registered");
        console.log(data);
        return this.props.ajax.get(true,'http://numerolog.privatebot.info:8080/forum/api/auth/');
      }
    )
    .then(
      (data) => {
        console.log("get result");
        console.log(data);
        this.props.setUserInfo(data);
        this.props.cbClose(true);
      }
    )
    .catch(
        (rejection) => {
        console.log(rejection);
        let str_error = '';
        if (rejection.hasOwnProperty('password')) {
          let str = 'Пароль: ';
          for (let i in rejection.password) {
            str += rejection.password[i] + ' ';
          }
          //alert(str);
          str_error += str +'\n';
        }
        if (rejection.hasOwnProperty('username')) {
          let str = 'Логин: ';
          for (let i in rejection.username) {
            str += rejection.username[i] + ' ';
          }
          //alert(str);
          str_error += str + '\n';
        }
        if (rejection.hasOwnProperty('email')) {
          let str = 'E-mail: ';
          for (let i in rejection.email) {
            str += rejection.email[i] + ' ';
          }
          //alert(str);
          str_error += str + '\n';
        }
        if (rejection.hasOwnProperty('captcha')) {
          let str = 'Captcha: ';
          for (let i in rejection.captcha) {
            str += rejection.captcha[i] + ' ';
          }
          //alert(str);
          str_error += str + '\n';
        }
        alert(str_error);
      }
    )
  }

  render () {
    return (
      <Modal style={{width : '100%'}} visible={this.props.visible}
        transparent={false} ariaHideApp={false}>
      {this.state.showreg === false ?
      <AuthForm passInput={this.passInput}
        emailInput={this.emailInput}
        auth={this.state.auth}
        cbClose={this.props.cbClose}
        loginPress={this.loginPress}
        registerPress={this.registerPress}/>
        :
        <RegisterPage passInput={this.passInput}
          userInput={this.userInput}
          emailInput={this.emailInput}
          captchaInput={this.captchaInput}
          question={this.state.captcha}
          auth={this.state.auth}
          cbClose={this.props.cbClose}
          toLoginPage={this.toLoginPage}
          scrollHeight = {window.innerHeight}
          registerPress={this.registerSubmitPress}/>
      }
    </Modal>
    )
  }


}

export default LoginPageContainer;
