class MessageList extends Component{
  constructor(props) {
    super(props);

    this.state = {
      selected : (new Map() : Map<string,boolean>),
      filter : 3,
      added_height : 0,
      message : '',
      messageNull : true,
      offset : 1,
      total_offset : 1,
      loading : true,
    };
    this.props.misago.do_fetch_postlist(this._setChatMessages,this.props.chat, this.props.user,1);
  }
  _keyExtractor = (item,index) => index.toString();

  _setChatMessages = (data,offset, total_offset) => {
    if (offset === 1)
      this.setState({data : data,offset : offset, total_offset : total_offset, loading : false});
    else
      this.setState({data : [...this.state.data, ...data],offset : offset, total_offset : total_offset, loading : false});
  }
  _handleLoadMore = () => {
    if ((this.state.offset < this.state.total_offset)&&(!this.state.loading)) {
      this.setState({loading : true});
      this.props.misago.do_fetch_postlist(this._setChatMessages,this.props.chat, this.props.user,this.state.offset + 1);
    }
  }

  _onPressItem = (index : int) => {
    this.setState((state)=> {
        const selected = (new Map() : Map<string,boolean>);
        selected.set(index,!selected.get(index));
        return {selected};
    }
    );
  }
  _changeFilter = (value) => {
    this.setState({filter : value});
  }

  _renderHeader = () => {
      return (
        <View style={{alignSelf : 'center',justifyContent : 'center',width : '100%',alignItems : 'center',backgroundColor : '#fff',minHeight : 48 }}>
          {
            this.props.user.is_authenticated ?
            <MessageInputAuth
              msgText={this.state.message}
              textMessageInput={this._InputTextMessage}
              attachMenu={this._openAttachMenu}
              added_height={this.state.added_height}
              sendMessage={this._msgSend}
              msgNull={this.state.messageNull} />
              :
              <MessageInputNoAuth login={this.props.login}/>

          }
        </View>
      )
  }
  _renderFooter = () => {
      return (
        <View style={{width:'100%',justifyContent : 'center',alignSelf : 'center',alignItems : 'center'}}>
        {
          this.state.loading ?
          <ActivityIndicator size="large" color="#0000ff" />
          :
            null
        }
        </View>
      )
  }


  _openAttachMenu = () => {
    console.log('attach pressed');
  }

  _msgSend = () => {

    this.props.misago.do_post_message(this.props.chat,this.state.message,[],this._postResult);
  }

  _postResult = (ret) => {
        try {

            let data = this.state.data;
            if (data[0].user_id === ret.user_id) {
              ret.duplicate = true;
              ret.self = true;
            }
            data.unshift(ret);
            this._InputTextMessage('');
            this.setState({data : data});
      } catch(err) {
          console.log(err);
        }
  }

  _InputTextMessage = (value) => {
    let v = value === '' ? true : false;
    this.setState({message : value, messageNull : v});

  }

  render () {
    return (
      <View style={{height : this.props.fullHeight}}>
        <MessageListHeader chat={this.props.chat} showChatList={this.props.showChatList}/>
        <View style={{flex : 9}}>
          <FlatList
            data={this.state.data}
            ref="flatList"
            style={[styles.wrapperList,{backgroundColor : '#F1F3F5' }]}
            inverted={true}
            renderItem={({item,index}) => {

              return (
                <MessageItem post={item} id={index}  key={index}
                  videoSize={{width : this.props.fullWidth * 0.65,height : this.props.fullWidth * 0.4875}}/>
              )
            }}
            onEndReached={this._handleLoadMore}
            onEndReachedThreshold={1}
            initialNumToRender={10}
            keyExtractor={this._keyExtractor}
            ListHeaderComponent={this._renderHeader}
            ListFooterComponent={this._renderFooter}
            stickyHeaderIndices={[0]} /* sticky list header to top */
          />
        </View>
      </View>

    )
  }


}

const styles = StyleSheet.create({
  wrapperList : {
    width : '100%',
    backgroundColor : '#fff',
  }

});

export default MessageList;
