import React,{Component} from 'react'
import {View, StyleSheet, TouchableOpacity,ScrollView } from 'react-native'
import Ptext from "../../../common/Ptext.js"
import Svg from 'react-native-svg'

function tttt() {

}

let profilePart = [
    {
      title : 'Профиль',
      cb : tttt,
      selected : true,
      url : 'user',
    },
    {
      title : 'Уведомления',
      cb : tttt,
      selected : false,
      url : 'notify',
    },
    {
      title : 'Заказы',
      cb : tttt,
      selected : false,
      url : 'orders',
    },
    {
      title : 'Работа',
      cb : tttt,
      selected : false,
      url : 'work',
    },
    {
      title : 'Счёт',
      cb : tttt,
      selected : false,
      url : 'account',
    },
    {
      title : 'Доступы',
      cb : tttt,
      selected : false,
      url : 'permission',
    },
]



class ProfileMenu extends Component {
  state = {
    menu : profilePart,
    selected : 0,
  }
  _selectcb = (id) => {
    for (let i in profilePart)
      profilePart[i].selected = false;
    profilePart[id].selected = true;
    this.props.history.push(this.props.match.url+'/'+profilePart[id].url);
    this.setState({menu : profilePart,selected : id});
  }

  render (){
    return(
      <View>
        {/* First header*/}
        <View style={[styles.wrapper,{flexDirection : this.props.horizontal ? 'row' : 'column'}]}>


        <Ptext style={styles.title}>Мой аккаунт</Ptext>
          {
            this.props.horizontal ?
          <TouchableOpacity onPress={this.props.cbLogOut} style={{marginRight : 14}}>
            <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M19 3H5C3.89 3 3 3.89 3 5V9H5V5H19V19H5V15H3V19C3 19.5304 3.21071 20.0391 3.58579 20.4142C3.96086 20.7893 4.46957 21 5 21H19C19.5304 21 20.0391 20.7893 20.4142 20.4142C20.7893 20.0391 21 19.5304 21 19V5C21 3.89 20.1 3 19 3ZM10.08 15.58L11.5 17L16.5 12L11.5 7L10.08 8.41L12.67 11H3V13H12.67L10.08 15.58Z" fill="#B4BBC2"/>
            </Svg>
          </TouchableOpacity>
          :
          null
         }
        </View>
        {/* Second header with menus*/}
        <ScrollView horizontal={this.props.horizontal} style={{width : window.innerWidth,minHeight : 36}}>
        {
          this.state.menu.map((item,index,arr) => {
            return (
            <TouchableOpacity onPress={this._selectcb.bind(this,index)} style={{flexDirection : this.props.horizontal ? 'column' : 'row',
              backgroundColor : !this.props.horizontal && item.selected ? 'rgba(180,187,194,0.2)' : '#F1F3F5' }}>
                { !this.props.horizontal ?
                  <View style={{width : 2, borderLeftColor : '#45b4e3',
                  borderLeftWidth : item.selected ? 2 : 0}}/>
                  :
                  null

                }
                <Ptext style={[styles.title_menu,{color : 'black', marginBottom : 2}]}>{item.title}</Ptext>
                {
                  this.props.horizontal
                  ?
                <View style={{width : '90%',
                    borderTopWidth : item.selected ? 2 : 0,
                    height : 4,
                    borderTopColor : '#45b4e3',
                    alignSelf : 'center'}}/>
                  :
                  null
                }
            </TouchableOpacity>
          );
          })
        }

        </ScrollView>
        {
          !this.props.horizontal ?
        <TouchableOpacity onPress={this.props.cbLogOut}>
          <Ptext style={styles.title_menu}>Выйти</Ptext>
        </TouchableOpacity>
        :
        null
        }
      </View>
    )
  }

}

const styles = StyleSheet.create({
  title : {
    fontSize : 20,
    lineHeight : 36,
    textAlign : 'left',
    flex : 9,
    paddingLeft : 16,
    fontWeight : 'bold',
  },
  wrapper : {
    width : '100%',
    alignItems : 'center',
    flex : 1,
    justifyContent : 'flex-start',
    minWidth : 320,
    minHeight : 36,
  },
  title_menu : {
    fontSize : 16,
    lineHeight : 24,
    textAlign : 'left',
    fontWeight : 'normal',
    marginLeft : 10,
    marginRight : 10,
  },
});

export default ProfileMenu;
