import React, { Component } from 'react';
import { View, StyleSheet,TouchableOpacity } from 'react-native';
import Ptext from "../../../common/Ptext.js"
import Svg from 'react-native-svg'

class ActionSchool extends Component {
  render () {
      return (
        <TouchableOpacity onPress={this.props.onPress}>
          <View style={styles.actionColumn}>
            <View style={styles.actionColumnIcon}>
              <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M19 19H5V5H19V19ZM19 3H5C4.46957 3 3.96086 3.21071 3.58579 3.58579C3.21071 3.96086 3 4.46957 3 5V19C3 19.5304 3.21071 20.0391 3.58579 20.4142C3.96086 20.7893 4.46957 21 5 21H19C19.5304 21 20.0391 20.7893 20.4142 20.4142C20.7893 20.0391 21 19.5304 21 19V5C21 3.89 20.1 3 19 3ZM16.5 16.25C16.5 14.75 13.5 14 12 14C10.5 14 7.5 14.75 7.5 16.25V17H16.5V16.25ZM12 12.25C12.5967 12.25 13.169 12.0129 13.591 11.591C14.0129 11.169 14.25 10.5967 14.25 10C14.25 9.40326 14.0129 8.83097 13.591 8.40901C13.169 7.98705 12.5967 7.75 12 7.75C11.4033 7.75 10.831 7.98705 10.409 8.40901C9.98705 8.83097 9.75 9.40326 9.75 10C9.75 10.5967 9.98705 11.169 10.409 11.591C10.831 12.0129 11.4033 12.25 12 12.25Z" fill="black"/>
              </Svg>
            </View>
            <View style={styles.actionLabel}>
              <Ptext style={styles.actionCaptionLabel}>
              Учебные группы
              </Ptext>
              {this.props.eventsCnt !== 0 ?
              <View style={styles.actionEventsLabel}>
              <Ptext style={styles.actionEventsText}>
              {this.props.eventsCnt}
              </Ptext>
              </View> : null }
            </View>
          </View>
        </TouchableOpacity>
      )
    }
}

const styles = StyleSheet.create({
  actionColumn : {
    width : '100%',
    height : 48,
    flexDirection : 'row'
  },
  actionColumnIcon : {
    width : 64,
    paddingLeft : 24,
    paddingTop : 12,
  },
  actionLabel : {
    flex : 1,
    flexDirection : 'row',
    alignItems : 'center',
    justifyContent : 'flex-start',
    paddingLeft : 5
  },
  actionCaptionLabel : {
    fontSize : 16
  },
  actionEventsView : {
    flex : 1,
  },
  actionEventsText : {
    borderRadius : 10,
    borderWidth : 1,
    borderColor : '#45B4E3',
    color : '#45B4E3',
    borderStyle : 'solid',
    textAlign : 'left',
    paddingLeft : 8,
    paddingRight : 8,
    marginLeft : 8,
    marginRight : 8,
    fontSize : 16
  }
})
export default ActionSchool;
