import React from 'react'
import {View} from 'react-native'
import Ptext from "../../../common/Ptext.js"
import { NavLink } from '../../router/react-router';

export default function ChatIcon({ title = 'Chat', style = {} , active = false, onPress = null, fontSize = 14 })
{
  return (
    <NavLink to="/chat" style={style}
        activeStyle={{
        fontWeight: "bold",
        color: '#1180AF'
      }}>
        {title}
    </NavLink>
  )
}
