import React,{Component} from 'react';
import {ActivityIndicator,View } from 'react-native';
import BlogList from './blogList.js'
import BlogMozaikList from './blogMozaikList.js'


var stories_debug = [
  {
    name : 'Первый',
  },
  {
    name : 'Второй',
  },
  {
    name : 'Третий',
  },
  {
    name : 'Четвертый',
  },
  {
    name : 'Пятый',
  },
  {
    name : 'Шестой',
  },
  {
    name : 'Седьмой',
  },
  {
    name : 'Восьмой',
  },
  {
    name : 'Девятый',
  },
  {
    name : 'Десятый',
  }
];

/*
<BlogTopHeader/>
<Stories items={stories_debug}/>
<BlogSecondHeader viewChanged={this._changeView}/>
{this.state.listView ?
  <BlogList data={this.props.data} scrollWidth={this.props.scrollWidth}
    showFullPost={this.props.showFullPost}
    openComments={this.props.openComments}
    />
  :
  <BlogMozaikList data={this.props.data} scrollWidth={this.props.scrollWidth}
    showFullPost={this.props.showFullPost}
    />
}

*/
class BlogPage extends Component{
  constructor(props){
    super(props);
    this.state = {
      listView : true,
    }
  }

  _changeView = (val) => {
    this.setState({listView : val});
  }

  render () {
    return (
      <View style={{justifyContent:'flex-start',alignItems:'center'}}>
      {this.state.listView ?
        <BlogList data={this.props.data} scrollWidth={this.props.scrollWidth}
          showFullPost={this.props.showFullPost}
          openComments={this.props.openComments}
          viewChanged={this._changeView}
          currentView={this.state.listView}
          t_height = {this.props.t_height}
          search={this.props.search}
          stories={stories_debug}
          />
          :
          <BlogMozaikList data={this.props.data} scrollWidth={this.props.scrollWidth}
            showFullPost={this.props.showFullPost}
            openComments={this.props.openComments}
            viewChanged={this._changeView}
            currentView={this.state.listView}
            t_height = {this.props.t_height}
            search={this.props.search}
            stories={stories_debug}
            />


      }
        <ActivityIndicator/>
      </View>
    )
  }
};
export default BlogPage;
