import React, { Component } from 'react';
import { StyleSheet, View } from 'react-native';
import OneCellItem from './oneCellItem.js'

class ThreeColumn extends Component {

  render() {
    return (
        <View style={[styles.column,this.props.style]}>
          {
            this.props.cells.map((prop,key,arr) => {
              return (
                <OneCellItem key={key} cell={prop} kn={(key % 3 !== 2) ? true : false}/>
              );
            })
          }
        </View>
    )
  }
};

const styles = StyleSheet.create({
  column : {
    flexDirection : 'column',
    backgroundColor : '#fff',
    minWidth : 70,
    minHeight : 210,
    borderRadius : 4,
    marginLeft : 4,
    marginTop : 4,
    marginBottom : 4,
    padding : 4,
    shadowOpacity: 0.2,
    shadowRadius: 2,
    shadowColor: '#000',
    shadowOffset: { height: 1, width: 0 },
  },
});

export default ThreeColumn;
