import React, { Component } from 'react';
import { View, Image, StyleSheet, TouchableOpacity } from 'react-native';
import Svg from 'react-native-svg';
import Button from "../../common/Button.js"
import Ptext from "../../common/Ptext.js"

class ExpertItem extends Component {


  render () {
    let full_name = this.props.info.firstName + ' ' + this.props.info.lastName;
    let experience = 'Опыт ' + this.props.info.experience + ' года';
    let sinceText = 'Запись ' + this.props.info.freeSince;
    let color = '#9aeb71';
    if (this.props.info.freeSince !== undefined)
      color = '#FFEECC';

    return (
      <View style={styles.wrapper}>

        <TouchableOpacity onPress={this.props.onExtendedInfo} style={styles.header}>
          <Image style={styles.headerAvatar} source={this.props.info.avatar}/>
          <View style={styles.headerInfo}>
            <Ptext>{full_name}</Ptext>
            <Ptext>{experience}</Ptext>
            <Ptext style={[styles.freeSince,{backgroundColor : color}]}>{this.props.info.freeSince === undefined ? 'Свободна' : sinceText}</Ptext>
          </View>
        </TouchableOpacity>

        <View style={styles.orders}>
          <Svg style={styles.ordersIcon} width="20" height="19" viewBox="0 0 20 19" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M18 4C18.58 4 19.05 4.2 19.42 4.59C19.8 5 20 5.45 20 6V17C20 17.55 19.8 18 19.42 18.41C19.05 18.8 18.58 19 18 19H2C1.42 19 0.95 18.8 0.58 18.41C0.2 18 0 17.55 0 17V6C0 5.45 0.2 5 0.58 4.59C0.95 4.2 1.42 4 2 4H6V2C6 1.42 6.2 0.95 6.58 0.58C6.95 0.2 7.42 0 8 0H12C12.58 0 13.05 0.2 13.42 0.58C13.8 0.95 14 1.42 14 2V4H18ZM2 6V17H18V6H2ZM12 4V2H8V4H12Z" fill="#B4BBC2"/>
          </Svg>
          <Ptext style={styles.ordersCaption}>
          {this.props.info.ordersCnt} заказов выполнено
          </Ptext>
        </View>

        <Ptext style={styles.descr}>
          {this.props.info.descr}
        </Ptext>

        <View style={styles.controls}>
          <Button style={styles.controlsChose}
            onPress={this.props.onSelect} title={'Выбрать'}/>
          <Button style={styles.controlsAsk}
            onPress={this.props.onAskQuestion}
            title={'Задать вопрос'}/>
        </View>

      </View>
    )
  }
}

const styles = StyleSheet.create({
  wrapper : {
    flexDirection : 'column',
  },
  header : {
    flexDirection : 'row',
    justifyContent : 'flex-start',
    alignItems : 'flex-start'
  },
  headerAvatar : {
    width : 64,
    height : 64,
    margin : 12
  },
  headerInfo : {
    justifyContent : 'space-between',
    alignItems : 'center',
    marginTop : 24,
  },
  freeSince : {
    borderRadius : 4,
  },
  orders : {
    flexDirection : 'row',
    justifyContent : 'flex-start',
    alignItems : 'center',
    marginBottom : 6,
  },
  ordersIcon : {
    marginLeft : 12,
    marginRight : 12,
  },
  ordersCaption : {

  },
  descr : {
    margin : 12,
    textAlign : 'left',
  },
  controls : {
    flexDirection : 'row',
    justifyContent : 'flex-start'
  },
  controlsChose : {
    marginLeft : 12,
    width : 94
  },
  controlsAsk : {
    backgroundColor : '#ffffff',
    marginLeft : 12

  },
});

export default ExpertItem;
