import React,{ Component } from 'react';
import { View, StyleSheet, TouchableOpacity, Image } from 'react-native';
import Ptext from "../../common/Ptext.js"
import Button from "../../common/Button.js"


class ServiceSelector extends Component {

  render () {
    return (
      <View style={[styles.wholeForm,this.props.style]}>
        <View style={styles.switchButtons}>
          <TouchableOpacity
          style={[styles.buttonLeft,
            {backgroundColor : this.props.selected ? '#FFCB66' : '#ffffff'}]}
            onPress={this.props.onChange}>
            <Ptext>Ольга Перцева</Ptext>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.buttonRight,
          {backgroundColor : this.props.selected ? '#ffffff' : '#FFCB66'}]}
          onPress={this.props.onChange}>
            <Ptext>Эксперты</Ptext>
          </TouchableOpacity>
        </View>
        <View style={styles.infoWrapper}>
          <Image source={require('../../icons/me-img-olga.png')} style={styles.infoIcon}/>
          <View style={styles.infoText}>
            <Ptext style={styles.text}>Тренер личностного роста, дипломированный психолог, нумеролог.</Ptext>
            <Ptext style={styles.text}>Авторская интерпретация психоматрицы Пифагора основанная исключительно на своей практике</Ptext>
            <View style={styles.controlsWrapper}>
              <Button style={styles.controlsAskQuestion}
                onPress={this.props.onAskQuestion.bind(this,-1)}
                title={'Задать вопрос'}/>
                <Button style={styles.controlsExtInfo}
                  onPress={this.props.onExtInfoShow.bind(this,0)}
                  styleTitle={styles.controlsExtInfoText}
                  title={'Узнать больше'}/>
            </View>
          </View>
        </View>
      </View>
    )
  }
}

const styles = StyleSheet.create({
  wholeForm : {
    flexDirection : 'column',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
    alignSelf : 'center',
    marginTop : 20,
  },
  switchButtons : {
    flexDirection : 'row',
    width : '90%',
    justifyContent : 'flex-start',
    alignItems : 'center',
    alignSelf : 'center',
  },
  buttonLeft : {
    width : '50%',
    borderWidth : 0,
    backgroundColor : '#FFCB66',
    padding : 12,
    borderTopLeftRadius : 4,
    borderBottomLeftRadius : 4,
  },
  buttonRight : {
    width : '50%',
    borderWidth : 0,
    backgroundColor : '#FFCB66',
    padding : 12,
    borderTopRightRadius : 4,
    borderBottomRightRadius : 4,
  },
  infoWrapper : {
    flexDirection : 'row',
    width : '100%',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
    alignSelf : 'center',
    marginTop : 20,
  },
  infoIcon : {
    alignSelf : 'flex-start',
    marginLeft : 12,
    width : 80,
    height : 80,

  },
  infoText : {
    flexDirection : 'column',
    paddingLeft : 12,
    justifyContent : 'flex-start',
    alignSelf : 'center',
    flexWrap : 'wrap',
    flexShrink : 1,
  },
  controlsWrapper : {
    flexDirection : 'row',
    width : '100%',
    justifyContent : 'flex-start',
    alignItems : 'center',
    alignSelf : 'center',
    flexWrap : 'wrap',
  },
  controlsAskQuestion : {
    backgroundColor : '#F1F3F5',
    borderColor : '#d2d6da',
    minWidth : 126,
    borderWidth  : 1,
  },
  controlsExtInfo : {
    minWidth : 175,
    backgroundColor : '#F1F3F5',
    borderColor : '#F1F3F5',
  },
  controlsExtInfoText : {
    color : '#1180AF',
    textAlign : 'left',
    alignSelf : 'flex-start',
    margin : 2,
  },
  text : {
    textAlign : 'left',
    flex : 1,
    flexWrap : 'wrap',
    width : '100%',
    flexShrink: 1,
    marginBottom : 6,
  }
});


export default ServiceSelector;
