import React, {Component} from 'react';
import { StyleSheet,ScrollView } from 'react-native';
import MatrixView from "../../components/matrix/MatrixView.js"
import CalculateForm from "../../components/matrix/CalculateForm.js"
import moment from 'moment';
class MatrixPageContainer extends Component {
  constructor(props) {
   super(props);

   // Assign state itself, and a default value for items
   this.state = {
     dateCalc : '',
     favorits_new_name : '',
   };
  }

  occurrences = (string, subString, allowOverlapping) => {

      string += "";
      subString += "";
      if (subString.length <= 0) return (string.length + 1);

      var n = 0,
          pos = 0,
          step = allowOverlapping ? 1 : subString.length;

      while (true) {
          pos = string.indexOf(subString, pos);
          if (pos >= 0) {
              ++n;
              pos += step;
          } else break;
      }
      return n;
  }

  date_valid = () => {

    if (this.state.date === undefined)
      return false;
    //TODO: check to string type
    return moment(this.state.date,["DD.MM.YYYY","D.MM.YYYY","DD.M.YYYY","D.M.YYYY"],true).isValid();
  }

  onFavoritsTextInputChanged = (value) => {

    this.setState({favorits_new_name : value});
  }

  onFavoritsSavePressed = () => {
    // push back to favorits array
    if (!this.date_valid())
      return; // TODO show error on input


    let favourits_item = {
      name : this.state.favorits_new_name,
      date : this.state.date
    }

    if (this.props.onAddFav) {
      this.props.onAddFav(favourits_item);
      this.setState({favorits_new_name : ''});
    }
  }

  onDateInputChanged = (value) => {
    let ppp = value.replace(/[^\d]/g, '');
    let days = ppp.slice(0,2);
    let mm = ppp.slice(2,4);
    let yy = ppp.slice(4);

    if (parseInt(days,10) > 31)
      days='31';
    if ((days.length)&&(days[0] > '3'))
        days = ("000" + parseInt(days,10)).slice(-2);

    if (parseInt(mm,10) > 12)
        mm='12';
    if ((mm.length)&&(mm[0]>'1'))
        mm = ("000" + parseInt(mm,10)).slice(-2);

    let tt = [];
    if (days.length)
      tt.push(days);
    if (mm.length)
        tt.push(mm);
    if (yy.length)
        tt.push(yy);
    ppp = tt.join('.');



    this.setState({date : ppp});
  }

  onGenderInputChanged = (value) => {
  }

  onCalcPress = () => {
    if (!this.date_valid()){
      return;
    }
    //this.calculateMatrix();
    this.props.onSelect({date : this.state.date, name : ''});
  }

  calculateMatrix = (ddata) => {
    if (ddata === undefined)
      return;
    console.log("Calc "+ddata);
    let s_all = ddata.split('.');
    let supp = [];
    let i = 0, s_whole='', summ = 0;
    for (i = 0; i < s_all.length; i++)
      s_whole += s_all[i];
    for (i = 0; i < s_whole.length; i++)
      summ += parseInt(s_whole[i],10);
    s_whole += summ;
    supp.push(summ);
    i = ((summ/10)|0)+(summ%10);
    s_whole += i;
    supp.push(i);
    for (i = 0; i < s_all[0].length; i++)
      if (s_all[0][i] !== '0') {
        i = 2*parseInt(s_all[0][i],10);
        break;
      }
    summ -= i;
    s_whole += summ;
    supp.push(summ);
    i = ((summ/10)|0)+(summ%10);
    s_whole += i;
    supp.push(i);
    let matrix = [];
    let three_cells = [];
    summ = 1;
    for (i = 1; i < 9+1; i++) {
      matrix.push({value : this.occurrences(s_whole,summ,false), number : summ});
      summ += 3;
      if (summ > 9)
        summ -= 8;
    }
    three_cells.push({value : matrix[0].value + matrix[1].value+matrix[2].value});
    three_cells.push({value : matrix[3].value + matrix[4].value+matrix[5].value});
    three_cells.push({value : matrix[6].value + matrix[7].value+matrix[8].value});

    let temp = {
      value : matrix[6].value + matrix[4].value + matrix[2].value
    };
    let house = {
      value : matrix[1].value + matrix[4].value + matrix[7].value
    };

    for (i = 0; i < matrix.length; i++)
      matrix[i].value = matrix[i].number.toString().repeat(matrix[i].value);

    this.setState({matrix : matrix});
    this.setState({threeCells : three_cells});
    this.setState({temp : temp});
    this.setState({house : house});
    this.setState({support_nums : supp});
    this.setState({lucky : supp[1]});
    this.setState({dateCalc : ddata});
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    if (newProps.item !== this.state.item){
      this.setState({date : newProps.item.date, favorits_new_name : newProps.item.name});
      this.calculateMatrix(newProps.item.date);

    }
    //this.setState({data : newProps.data});
  }


  render () {
    let cb_calulate_form = {
      cbDateInput : this.onDateInputChanged,
      cbGenderInput : this.onGenderInputChanged,
      cbCalcPress : this.onCalcPress
    }
    let cb_favorits_form = {
      cbNameInput : this.onFavoritsTextInputChanged,
      cbSavePress : this.onFavoritsSavePressed,
      value : this.state.favorits_new_name,
    }
    let cells = {
      matrix : this.state.matrix,
      threeCells : this.state.threeCells,
      temp : this.state.temp,
      house : this.state.house,
      support_nums : this.state.support_nums,
      lucky : this.state.lucky
    }
    let bidValue = this.state.date;
    return (
      <ScrollView contentContainerStyle={styles.page} style={{height:this.props.t_height}}>
        <CalculateForm supp={this.props.supp} cb={cb_calulate_form} bidValue={bidValue} openFav={this.props.openFav}/>
        <MatrixView supp={this.props.supp} cb={cb_favorits_form} cells={cells} />
      </ScrollView>
    );
  }
}
//
const styles = StyleSheet.create({
  page : {
    flexDirection : 'column',
    backgroundColor: '#F1F3F5',
    alignItems : 'center',
    alignContent: 'flex-start',
    justifyContent : 'flex-start'
  }
})

export default MatrixPageContainer;
