import React, { Component } from 'react';
import { StyleSheet, View } from 'react-native';
import OneCellItem from './oneCellItem.js'

class MatrixCells extends Component {

  render() {
    return (
        <View style={[styles.matrix,this.props.style]}>
          {
            this.props.cells.map((prop,key,arr) => {
              return (
                  <OneCellItem key={key} cell={prop} style={styles.cell} kn={(key  < 6 ) ? true : false}/>

              );
            })
          }
        </View>
    )
  }
};

const styles = StyleSheet.create({
  matrix : {
    flexDirection : 'row',
    backgroundColor : '#fff',
    minWidth : 210,
    flexWrap : 'wrap',
    minHeight : 210,
    borderRadius : 4,
    marginRight : 4,
    marginTop : 4,
    marginBottom : 4,
    padding : 4,
    shadowOpacity: 0.2,
    shadowRadius: 2,
    shadowColor: '#000',
    shadowOffset: { height: 1, width: 0 },
  },
  cell : {
    width:'33.3%'
  }
});

export default MatrixCells;
