import React, { Component } from 'react';
import { StyleSheet } from 'react-native';
import Button from "../../../common/Button.js"
//import { Button } from '@kiwicom/universal-components' // ugly shit without any use of styling for text/background colors

class ItemCaculateButton extends Component {
  constructor(props) {
    super(props);

    this.debugPress = this.debugPress.bind(this);
  }

  debugPress() {
    if (this.props.onPress !== undefined) {
      this.props.onPress();
    }
  }

  render() {
    return (
    <Button title='Рассчитать' onPress={this.debugPress} style={[styles.button,this.props.style]}/>
    )
  }
}

const styles = StyleSheet.create({
    button : {
      minWidth : 139,
    }
});


export default ItemCaculateButton;
