import React, { Component } from 'react';
import { StyleSheet, TouchableOpacity} from 'react-native';
import Ptext from '../../../common/Ptext.js'

class OneCellItem extends Component {

  render() {
    return (
        <TouchableOpacity style={[styles.cell,this.props.style]}>
            <Ptext style={styles.value}>{this.props.cell.value === 0 ? '-' : this.props.cell.value}</Ptext>
            <Ptext style={styles.descr}>{this.props.cell.descr}</Ptext>
        </TouchableOpacity>
    )
  }
};

const styles = StyleSheet.create({
  cell : {
    flexDirection : 'column',
    justifyContent : 'space-evenly',
    alignItems : 'flex-start',
    paddingLeft : 5,
    backgroundColor : '#fff',
    minHeight : 70,
    minWidth : 69,

  },
  value : {
    color : '#000',
    fontSize : 14
  },
  descr : {
    color : '#B4BBC2',
    fontSize : 10
  }
});

export default OneCellItem;
