import React,{Component} from 'react';
import {View, TextInput, StyleSheet, Image} from 'react-native';
import Ptext from "../../common/Ptext.js"
import Button from "../../common/Button.js"


class NotAuthFormName extends Component{
  render() {
    return (

      <View style={[styles.wrapper]}>
        <Ptext style={{alignSelf : 'baseline'}}>От: </Ptext>
        <TextInput style={styles.textInputLine} onChange={this.props.inputName}/>
      </View>

    );
  }
}
class NotAuthFormEmail extends Component{
  render() {
    return (

      <View style={[styles.wrapper]}>
        <Ptext style={{alignSelf : 'baseline'}}>E-Mail: </Ptext>
        <TextInput style={styles.textInputLine} onChange={this.props.inputEmail}/>
      </View>
    );
  }
}


class AskQuestionFrom extends Component{
  constructor(props) {
    super(props);

    this.state = {
      comment : '',
      name : '',
      auth : this.props.auth,
      val : 1,
      email : '',
    };
  }

  messageChange = (value) => {
    this.setState({comment : value});
  }
  inputName = (value) => {
    this.setState({name : value});
  }
  inputEmail = (value) => {
    this.setState({email : value});
  }

  render() {
    let full_name = '';
    let av = '';
    if (this.props.to === undefined ) {
      full_name = 'Ольга Перцева';
      av = require('../../icons/me-img-olga.png');
    }
    else {
      full_name = this.props.to.firstName + ' ' + this.props.to.lastName;
      av = this.props.to.avatar;
    }

    return (
      <View style={[styles.outWrapper,this.props.style]}>
        {this.props.auth ? null :
          <NotAuthFormName/>}
        {this.props.auth ? null :
            <NotAuthFormEmail/>}


        <View style={[styles.wrapper]}>
          <Image source={av} style={styles.avatar}/>
          <View style={styles.wrapperData}>
            <Ptext style={styles.nameText}>{full_name}</Ptext>
          </View>
        </View>
        <TextInput multiline={true}
            placeholder={'Введите интересующий Вас вопрос'}
            style={styles.textInput}
            onChangeText={this.messageChange}/>
        <View style={{width : '90%',alignSelf : 'center'}}>
          <Button title={'Отправить'} style={styles.buttonStyle} onPress={this.props.onSubmit}/>
        </View>
      </View>
    )
  }

}

const styles = StyleSheet.create({
  outWrapper : {
    flexDirection : 'column',
    width : '90%',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
    alignSelf : 'center',
    backgroundColor : '#ffffff',
  },
  wrapper : {
    flexDirection : 'row',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
    alignSelf : 'center',
    width : '90%',
    borderRadius : 4,
    marginTop : 12,
  },
  avatar : {
    width : 48,
    height : 48,
    marginTop : 14,
    marginLeft : 15,
  },
  nameText : {
    fontSize : 16,
    fontWeight : 'bold',
    lineHeight : 24,
  },
  wrapperData : {
    flexDirection : 'column',
    justifyContent : 'space-between',
    paddingTop : 14,
    paddingLeft : 14,
    height : 95,
  },
  textInput : {
    width : '90%',
    borderRadius : 4,
    borderWidth : 1,
    alignSelf : 'center',
    flex : 1,
    borderColor : '#B4BBC2',
    minHeight : 120,
    padding : 8,
  },
  textInputLine : {
    width : '90%',
    borderRadius : 4,
    borderWidth : 1,
    alignSelf : 'baseline',
    flex : 1,
    borderColor : '#B4BBC2',
    padding : 8,
  },
  buttonStyle : {
    minHeight : 32,
    height : 32,
    alignSelf : 'flex-end',
  }




});

export default AskQuestionFrom;
