import React, { Component } from 'react';
import { View, StyleSheet, TouchableOpacity, Image } from 'react-native';
import Ptext from "../../../common/Ptext.js"
import Svg from 'react-native-svg';

/*
<TouchableOpacity style={[styles.horizontalWrapper,{backgroundColor : this.props.selected ? '#E5E5E5' : '#fff'}]} onPress={this._onPress}>
  <View style={[styles.horizontalWrapper,{width:'90%', alignSelf : 'center',justifyContent:'flex-start'}]}>
  <Image style={styles.chatAvatar} source={this.props.chat.avatar_url}/>
  <View style={styles.verticalWrapper}>
    <Ptext style={styles.chatCaption}>{this.props.chat.chatName}</Ptext>
  </View>
  </View>
</TouchableOpacity>
*/


class MessageItem extends Component {
  // default constructor
  render_video = (size_w,size_h,url) => {
    return (
      <iframe src={url} width={size_w} height={size_h} frameBorder="0" allow="autoplay; fullscreen" allowFullScreen></iframe>
    )
  }

  render () {
    let align = this.props.post.self ? 'flex-end' : 'flex-start';
    let backColor = this.props.post.self ? '#D0F1FF' : '#fff';
    let marginTVal = this.props.post.duplicate ? 4 : 32;
    let marginLeftDuplicate = this.props.post.duplicate ? 32+38 : 0;

    return (
      <View style={[styles.horizontalWrapper,{alignSelf : align,justifyContent : align ,marginTop : marginTVal}]}>
          { !this.props.post.self && !this.props.post.duplicate ?
            <TouchableOpacity style={{marginLeft : 16,alignSelf : 'flex-start'}}>
              <Image style={styles.chatAvatar} source={this.props.post.avatar_url}/>
            </TouchableOpacity>
            : null
          }
          {
            !this.props.post.self && !this.props.post.duplicate ?
              <Svg style={{alignSelf:'flex-start'}} width="22" height="20" viewBox="-7 0 22 20" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M15.0001 19.5C15.0001 19.5 15.0001 10 4.00003 4.00001C8.5 3.5 15.0001 6.5 15.0001 6.5L15.0001 19.5Z" fill="white"/>
              </Svg>
            : null
          }
          <View style={[styles.verticalWrapper,{alignSelf : align,
                justifyContent : align ,
                backgroundColor : backColor,
                width : '65%',
                 borderRadius : 4,
                 marginLeft : marginLeftDuplicate,
                marginRight : 16}]}>
            { !this.props.post.duplicate && !this.props.post.self ?
            <Ptext style={{marginLeft : 10, fontWeight : 'bold',marginTop : 6,marginBottom : 6}}>{this.props.post.user}</Ptext>
            : null
            }
            <Ptext style={{marginLeft : 10, marginRight : 10, textAlign : 'left',
            fontSize : 14}}>{this.props.post.content}</Ptext>
          { this.props.post.video_url ?
            <Ptext>
              {this.render_video(this.props.vidoSize.width,this.props.vidoSize.height,this.props.post.video_url)}
            </Ptext>
            :
            null
            }
            <Ptext style={{marginTop : 6, marginBottom : 4, marginLeft : 10, marginRight : 10, textAlign : 'right',
            fontSize : 14,alignSelf : 'flex-end', color : '#B4BBC2'}}>{this.props.post.date}</Ptext>
          </View>
      </View>
    )
  }

};

const styles = StyleSheet.create({
  horizontalWrapper : {
    width : '100%',
    alignSelf : 'center',
    justifyContent : 'center',
    alignItems : 'center',
    flexDirection : 'row',
  },
  verticalWrapper : {
    width : '100%',
    alignSelf : 'center',
    justifyContent : 'space-between',
    alignItems : 'flex-start',
    flexDirection : 'column',
    marginLeft : 16,
  },
  chatAvatar : {
    width : 32,
    height : 32,
  },
  chatCaption : {
    fontSize : 16,
    lineHeight : 24,
  },
  chatLastMessage : {
    fontSize : 12,
    lineHeight : 16,
    color : '#B4BBC2',
  },
  chatLastActivity : {
    fontSize : 12,
    lineHeight : 16,
    color : '#B4BBC2',
  },
  chatNotifyCnt : {
    color : '#1180AF',
  },
  chatNotifyOuter : {
    borderRadius : 10,
    borderWidth : 1,
    borderColor : '#45B4E3',
    height : 20,
    paddingLeft : 4,
    paddingRight : 4,
    alignItems : 'center',
    justifyContent : 'center',
  }

});

export default MessageItem;
