import React,{Component} from 'react'
import AskQuestionForm from "../../components/contact/askQuestionForm.js"

class ContactContainer extends Component {


  onSubmit = (message) => {
    console.log('message to expert '+message);
  }
  onClose = () => {
    this.props.onClose();
  }

  render() {
    return (
        <AskQuestionForm to={this.props.to}
          onSubmit={this.props.onSubmit}
          onClose={this.onClose}
          style={{width:'100%'}}
          />
    )
  }
}
export default ContactContainer;
