import React from 'react'
import { Image, StyleSheet } from 'react-native'
import { TouchableOpacity } from 'react-native';

export default function ProfileIcon({ title = 'Profile', style = {}, onPress = null  })
{
  return (
    <TouchableOpacity onPress={onPress} style={[ styles.icon, style ]}>
      <Image source={require('./avatar.jpg')} style={[ styles.icon, style ]}/>
    </TouchableOpacity>
  )
}

const styles = StyleSheet.create({
  icon: {
    height: 24,
    width: 24,
    alignItems: 'center',
    backgroundColor : '#fff',
  }
})
