import React, { Component } from 'react';
import { Image, TouchableOpacity } from 'react-native';

class BlogMozaikItem extends Component {

  onItemPressed = () => {
    if (this.props.showFullPost)
      this.props.showFullPost(this.props.id);
  }

  render() {
    let size = this.props.scrollWidth/3;
    return(
      <TouchableOpacity onPress={this.onItemPressed}>
        <Image source={this.props.item.imgs[0].url} style={{width:size,height:size}}/>
      </TouchableOpacity>
    )
  }
}

export default BlogMozaikItem;
