import React, { Component } from 'react';
import { StyleSheet, TouchableOpacity} from 'react-native';
import Ptext from '../../../common/Ptext.js'

class OneCell extends Component {

  render() {
    return (
        <TouchableOpacity style={[styles.cell,this.props.style]}>
            <Ptext style={styles.value}>{this.props.cell.value === 0 ? '-' : this.props.cell.value}</Ptext>
            <Ptext style={styles.descr}>{this.props.cell.descr}</Ptext>
        </TouchableOpacity>
    )
  }
};

const styles = StyleSheet.create({
  cell : {
    flexDirection : 'column',
    justifyContent : 'space-evenly',
    alignItems : 'flex-start',
    paddingLeft : 10,
    backgroundColor : '#fff',
    minWidth : 70,
    minHeight : 70,
    borderRadius : 4,
    padding : 4,
    marginTop : 4,
    marginBottom : 4,
    shadowOpacity: 0.2,
    shadowRadius: 2,
    shadowColor: '#000',
    shadowOffset: { height: 1, width: 0 },
  },
  value : {
    color : '#000',
    fontSize : 14,
  },
  descr : {
    color : '#B4BBC2',
    fontSize : 10
  }
});

export default OneCell;
