import React, { Component } from 'react';
import { StyleSheet, View, TouchableOpacity } from 'react-native';
import Ptext from '../../../common/Ptext.js'

class MatrixInfo extends Component {

  render() {
    return (
        <View style={[styles.wrapper,this.props.style]}>
          <View style={styles.support}>
            <Ptext style={styles.captionText}>Доп. числа: </Ptext>
            { this.props.support_nums !== undefined ?
            <Ptext style={styles.valueText}>{
                this.props.support_nums.map((prop,key,arr) => {
                  if (key === arr.length - 1)
                   return ( prop );
                   else
                    return ( prop + ', ');
                }
              )

              }</Ptext>
              : null
            }
          </View>
          <TouchableOpacity style={styles.lucky}>
            <View style={[styles.lucky,{width : '100%'}]}>
                <Ptext style={styles.captionText}>Число судьбы: </Ptext>
                {this.props.lucky !== undefined ?
                  <Ptext style={styles.valueText}>{this.props.lucky}</Ptext>
                  : null}
            </View>
          </TouchableOpacity>
        </View>
    )
  }
};

const styles = StyleSheet.create({
  wrapper : {
    flexDirection : 'row',
    backgroundColor : '#cccfd1',
//    backgroundColor : '#b4bbc2',
    minWidth : 217,
    borderRadius : 4,
    minHeight : 70,
    marginTop : 4,
    marginBottom : 8,
    marginRight : 6
  },
  support : {
    width : '50%',
    flexDirection : 'row',
    flexWrap :'wrap',
    justifyContent : 'flex-start',
    alignItems : 'stretch'
  },
  lucky :  {
    width : '50%',
    flexDirection : 'row',
    flexWrap :'wrap',
  },
  captionText : {
    fontSize : 12
  },
  valueText : {
    fontSize : 14,
    fontStyle : 'bold',
  }

});

export default MatrixInfo;
