import React, { Component } from 'react';
import { View, StyleSheet, TouchableOpacity, Image } from 'react-native';
import Ptext from "../../../common/Ptext.js"

class ChatLastMessage extends Component {
  render () {
    return (
      <Ptext style={styles.chatLastMessage}>{this.props.content}</Ptext>
    )
  }
}

class ChatLastActivity extends Component {
  render () {
    if (this.props.content.cnt === 0) {
      return (
        <Ptext style={styles.chatLastActivity}>12:23</Ptext>
      )
    }else {
      return (
        <View style={styles.chatNotifyOuter}>
          <Ptext style={styles.chatNotifyCnt}>{this.props.content.cnt}</Ptext>
        </View>
      )
    }

  }
}


class ContactItem extends Component {
  // default constructor
  _onPress = () => {
    if (this.props.onPressItem)
      this.props.onPressItem(this.props.id);
  }
  render () {
    return (
      <TouchableOpacity style={[styles.horizontalWrapper,{backgroundColor : this.props.selected ? '#E5E5E5' : '#fff'}]} onPress={this._onPress}>
        <View style={[styles.horizontalWrapper,{width:'90%', alignSelf : 'center',justifyContent:'flex-start'}]}>
        <Image style={styles.chatAvatar} source={this.props.chat.avatar_url}/>
        <View style={styles.verticalWrapper}>
          <Ptext style={styles.chatCaption}>{this.props.chat.chatName}</Ptext>
          <ChatLastMessage content={this.props.chat.lastMessage}/>
        </View>
        <ChatLastActivity content={this.props.chat.notify}/>
        </View>
      </TouchableOpacity>
    )
  }

};

const styles = StyleSheet.create({
  horizontalWrapper : {
    width : '100%',
    alignSelf : 'center',
    justifyContent : 'center',
    alignItems : 'center',
    flexDirection : 'row',
    height : 64,
  },
  verticalWrapper : {
    width : '100%',
    flex : 2,
    alignSelf : 'center',
    justifyContent : 'space-between',
    alignItems : 'flex-start',
    flexDirection : 'column',
    marginLeft : 16,
  },
  chatAvatar : {
    width : 32,
    height : 32,
  },
  chatCaption : {
    fontSize : 16,
    lineHeight : 24,
  },
  chatLastMessage : {
    fontSize : 12,
    lineHeight : 16,
    color : '#B4BBC2',
  },
  chatLastActivity : {
    fontSize : 12,
    lineHeight : 16,
    color : '#B4BBC2',
  },
  chatNotifyCnt : {
    color : '#1180AF',
  },
  chatNotifyOuter : {
    borderRadius : 10,
    borderWidth : 1,
    borderColor : '#45B4E3',
    height : 20,
    paddingLeft : 4,
    paddingRight : 4,
    alignItems : 'center',
    justifyContent : 'center',
  }

});

export default ContactItem;
