import React,{Component} from 'react'
import {View,StyleSheet,Text, ScrollView} from 'react-native'
import ProfileUser from '../tabs/profileUser.js'
import ProfileNotify from '../tabs/profileNotify.js'
import ProfileOrders from '../tabs/profileOrders.js'
import ProfileWork from '../tabs/profileWork.js'
import ProfileAccount from '../tabs/profileAccount.js'
import ProfilePermissions from '../tabs/profilePermissions.js'
import { Router, Route } from "../../router/react-router"

class ProfileContent extends Component {

  _renderProfile = ({match,history,location}) => {
    if (match.params.subPath === 'user') {
      return (
        <ProfileUser/>
      )
    }
    if (match.params.subPath === 'notify') {
      return (
        <ProfileNotify/>
      )
    }
    if (match.params.subPath === 'orders') {
      return (
        <ProfileOrders/>
      )
    }
    if (match.params.subPath === 'work') {
      return (
        <ProfileWork/>
      )
    }
    if (match.params.subPath === 'account') {
      return (
        <ProfileAccount/>
      )
    }
    if (match.params.subPath === 'permission') {
      return (
        <ProfilePermissions/>
      )
    }

  }


  render () {
    let cc = this.props.match.path;
    return (
        <ScrollView style={styles.wrapper,{height : this.props.fullHeight}} contentContainerStyle={styles.container}>
          <Route path={cc+'/:subPath'} render={this._renderProfile} style={{width:'100%'}}/>
        </ScrollView>
    )
  }


}

const styles = StyleSheet.create({
  wrapper : {
    width : '100%',
    alignSelf : 'center',
    backgroundColor : '#F1F3F5',
  },
  container : {
    justifyContent : 'flex-start',
    alignItems : 'center',
  }
});

export default ProfileContent;
