import React, { Component } from 'react';
import { View, StyleSheet,TouchableOpacity } from 'react-native';
import Svg from 'react-native-svg';

class BlogSecondHeader extends Component {

  switch_to_lview = () => {
    if (this.props.currentView)
      return;

    let val = !this.props.currentView;
    if (this.props.viewChanged){
      this.props.viewChanged(val);
    }
  }
  switch_to_mview = () => {
    if (!this.props.currentView)
      return;
    let val = !this.props.currentView;
    if (this.props.viewChanged){
      this.props.viewChanged(val);
    }
  }

  render () {
    let f_1 = '#000';
    let f_2 = '#B4BBC2';

    if (!this.props.currentView){
      let f_3 = f_2;
      f_2 = f_1;
      f_1 = f_3;
    }


    return (
        <View style={styles.wrapper}>
          <TouchableOpacity onPress={this.switch_to_lview} style={styles.icons}>
                  <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <rect x="4" y="3" width="16" height="2" fill={f_1}/>
                  <rect x="4" y="19" width="16" height="2" fill={f_1}/>
                  <path fill-rule="evenodd" clip-rule="evenodd" d="M4 7C3.44772 7 3 7.44772 3 8V16C3 16.5523 3.44772 17 4 17H20C20.5523 17 21 16.5523 21 16V8C21 7.44772 20.5523 7 20 7H4ZM19 9H5V15H19V9Z" fill={f_1}/>
                  </Svg>
          </TouchableOpacity>
          <TouchableOpacity onPress={this.switch_to_mview} style={styles.icons}>
                  <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M10 4V8H14V4H10ZM16 4V8H20V4H16ZM16 10V14H20V10H16ZM16 16V20H20V16H16ZM14 20V16H10V20H14ZM8 20V16H4V20H8ZM8 14V10H4V14H8ZM8 8V4H4V8H8ZM10 14H14V10H10V14ZM4 2H20C20.5304 2 21.0391 2.21071 21.4142 2.58579C21.7893 2.96086 22 3.46957 22 4V20C22 20.5304 21.7893 21.0391 21.4142 21.4142C21.0391 21.7893 20.5304 22 20 22H4C2.92 22 2 21.1 2 20V4C2 3.46957 2.21071 2.96086 2.58579 2.58579C2.96086 2.21071 3.46957 2 4 2Z" fill={f_2}/>
                  </Svg>
          </TouchableOpacity>
        </View>
    )
  }
}

const styles = StyleSheet.create({
  wrapper : {
    width : '100%',
    height : 32,
    marginTop : 13,
    marginBottom:13,
    alignItems : 'center',
    flexDirection : 'row',
    justifyContent : 'space-around'
  },
  icons : {
    marginLeft : 13,
    marginRight : 13,

  }
});

export default BlogSecondHeader;
