export class Wagtail {
  constructor(ajax){
    this.ajax = ajax;
    this.pages_url = '/api/v2/pages';
    this.images_url = '/api/v2/images';
    this.all_blog_posts_small = '/?type=blog.PostPage&fields=header_image';
    this.all_blog_posts_desc = '/?type=blog.PostPage&fields=header_image,body';
    this.all_blog_posts = '/?type=blog.PostPage';
    this.all_fields='&fields=header_image,body,tags';
    this.tags_filter='&tags=';
    this.order_by_new='&order=-first_published_at'
    this.offset='&offset='
    this.limit='&limit='
    this.debug_url_blog = 'http://blog.numerolog.privatebot.info:8080'
    this.debug_url_services = 'http://serv.numerolog.privatebot.info:8080'
    this.all_services_posts = '/?type=blog.ServicePage&fields=*';
    this.search='&search='
  };

  debug_fetch(cbGet) {
    let url_all_desc = this.debug_url_blog+this.pages_url+this.all_blog_posts_desc+this.order_by_new;
    this.ajax.get(false,url_all_desc).then(
      (data) => {
        let new_data = [];
        for (let ci in data.items){
          let i = data.items[ci];
          let nobj = {};
          let imgs = [];
          if (i.header_image === null)
            continue;
          imgs.push({url : this.debug_url_blog+i.header_image.meta.download_url});
          nobj.id = i.id;
          nobj.title=i.title;
          nobj.body=i.body.replace(/(<|&lt;)br\s*\/*(>|&gt;)/g,'\n').replace(/(<|&lt;)p(>|&gt;)/g,'\t').replace(/(<|&lt;)\/p(>|&gt;)/g,'\n');
          nobj.body=nobj.body.replace(/&quot\;/g,'"');
          nobj.imgs = imgs;
          nobj.html_url = i.meta.html_url;
          nobj.slug = i.meta.slug;
          new_data.push(nobj);
        }
        cbGet(new_data);
      }
    )
    .catch(
      (rejection) => {
        console.log(rejection);
      }
    )
  }
  debug_fetch_services(cbGet) {
    let url_all_desc = this.debug_url_services+this.pages_url+this.all_services_posts;
    this.ajax.get(false,url_all_desc).then(
      (data) => {
        let new_data_experts = [];
        let new_data_olga = [];
        for (let ci in data.items){
          let i = data.items[ci];
          let nobj = {};
          let imgs = [];

          nobj.id = i.id;
          nobj.name=i.title;
          nobj.descr=i.descr.replace(/(<|&lt;)br\s*\/*(>|&gt;)/g,'\n').replace(/(<|&lt;)p(>|&gt;)/g,'\t').replace(/(<|&lt;)\/p(>|&gt;)/g,'\n');
          nobj.descr=nobj.descr.replace(/&quot\;/g,'"');
          nobj.html_url = i.meta.html_url;
          nobj.price=i.price;
          nobj.expert=i.expert;
          nobj.persCount = i.date_cnt;
          nobj.slug = i.meta.slug;
          if (nobj.expert) new_data_experts.push(nobj);
            else new_data_olga.push(nobj);
        }
        cbGet(new_data_experts,new_data_olga);
      }
    )
    .catch(
      (rejection) => {
        console.log(rejection);
      }
    )
  }


  debug_search(cbGet,search) {
    if (search.length === 0)
      return this.debug_fetch(cbGet);

    let url_all_desc = this.debug_url_blog+this.pages_url+this.all_blog_posts+this.search+search+this.all_fields+this.order_by_new;


    this.ajax.get(false,url_all_desc).then(
      (data) => {
        console.log("fetch posts result");
        let new_data = [];
        for (let ci in data.items){
          let i = data.items[ci];
          let nobj = {};
          let imgs = [];
          if (i.header_image === null)
            continue;
          imgs.push({url : this.debug_url+i.header_image.meta.download_url});
          nobj.id = i.id;
          nobj.title=i.title;
          nobj.body=i.body.replace(/(<|&lt;)br\s*\/*(>|&gt;)/g,'\n').replace(/(<|&lt;)p(>|&gt;)/g,'\t').replace(/(<|&lt;)\/p(>|&gt;)/g,'\n');
          nobj.body=nobj.body.replace(/&quot\;/g,'"');
          nobj.imgs = imgs;
          nobj.html_url = i.meta.html_url;
          nobj.slug = i.meta.slug;
          new_data.push(nobj);
        }
        cbGet(new_data);
      }
    )
    .catch(
      (rejection) => {
        console.log(rejection);
      }
    )
  }


};
