import React, { Component } from 'react';
import { StyleSheet,View, TextInput,TouchableOpacity } from 'react-native';
import PropTypes from 'prop-types';



class MessageInputAuth extends Component{

    render () {
    return (
        <View style={{minHeight : 48 + this.props.added_height,
               backgroundColor : '#fff',
               flexDirection : 'row',
               flex : 1,
               alignItems : 'center',
               width : '90%',
               flexWrap : 'wrap',
               justifyContent : 'flex-start'}}>
          <View style={{flex : 2, marginLeft : 15}}>
          <TextInput
            placeholder="Введите сообщение"
            value={this.props.msgText}
            onChangeText={this.props.textMessageInput}
            multiline={true}
            style={{height:'auto'}}
            />
          </View>
          {
            this.props.msgNull ?
            <TouchableOpacity onPress={this.props.attachMenu} >
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M19.0711 11.7929C20.1025 12.8243 20.682 14.2233 20.682 15.682C20.682 17.1407 20.1025 18.5396 19.0711 19.5711C18.0396 20.6025 16.6407 21.182 15.182 21.182C13.7233 21.182 12.3244 20.6025 11.2929 19.5711L3.8683 12.1464C3.11815 11.3963 2.69673 10.3789 2.69673 9.31801C2.69673 8.25714 3.11815 7.23973 3.8683 6.48958C4.61844 5.73944 5.63586 5.31801 6.69673 5.31801C7.75759 5.31801 8.77501 5.73944 9.52515 6.48958L15.5356 12.5C16.0044 12.9688 16.2678 13.6047 16.2678 14.2678C16.2678 14.9308 16.0044 15.5667 15.5356 16.0355C15.0667 16.5044 14.4308 16.7678 13.7678 16.7678C13.1048 16.7678 12.4689 16.5044 12 16.0355L6.69673 10.7322L7.75739 9.67156L13.0607 14.9749C13.2482 15.1624 13.5026 15.2678 13.7678 15.2678C14.033 15.2678 14.2874 15.1624 14.4749 14.9749C14.6624 14.7873 14.7678 14.533 14.7678 14.2678C14.7678 14.0025 14.6624 13.7482 14.4749 13.5606L8.46449 7.55024C8.23235 7.3181 7.95675 7.13395 7.65343 7.00831C7.35012 6.88267 7.02503 6.81801 6.69673 6.81801C6.36842 6.81801 6.04333 6.88267 5.74002 7.00831C5.4367 7.13395 5.16111 7.3181 4.92896 7.55024C4.69681 7.78239 4.51266 8.05799 4.38703 8.3613C4.26139 8.66462 4.19673 8.9897 4.19673 9.31801C4.19673 9.64631 4.26139 9.9714 4.38703 10.2747C4.51266 10.578 4.69681 10.8536 4.92896 11.0858L12.3536 18.5104C13.1037 19.2605 14.1211 19.682 15.182 19.682C16.2429 19.682 17.2603 19.2605 18.0104 18.5104C18.7606 17.7603 19.182 16.7428 19.182 15.682C19.182 14.6211 18.7606 13.6037 18.0104 12.8535L11.2929 6.13603L12.3536 5.07537L19.0711 11.7929Z" fill="#B4BBC2"/>
            </svg>
            </TouchableOpacity>
            :
            <TouchableOpacity onPress={this.props.sendMessage} >
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M22 12C22 14.6522 20.9464 17.1957 19.0711 19.0711C17.1957 20.9464 14.6522 22 12 22C10.6868 22 9.38642 21.7413 8.17317 21.2388C6.95991 20.7362 5.85752 19.9997 4.92893 19.0711C3.05357 17.1957 2 14.6522 2 12C2 9.34784 3.05357 6.8043 4.92893 4.92893C6.8043 3.05357 9.34784 2 12 2C13.3132 2 14.6136 2.25866 15.8268 2.7612C17.0401 3.26375 18.1425 4.00035 19.0711 4.92893C19.9997 5.85752 20.7362 6.95991 21.2388 8.17317C21.7413 9.38642 22 10.6868 22 12ZM6 13H14L10.5 16.5L11.92 17.92L17.84 12L11.92 6.08L10.5 7.5L14 11H6V13Z" fill="#45B4E3"/>
            </svg>
            </TouchableOpacity>

          }

        </View>
      )
    }
}

MessageInputAuth.propTypes = {
  msgText : PropTypes.string.isRequired,
  textMessageInput : PropTypes.func.isRequired,
  attachMenu : PropTypes.func.isRequired,
  sendMessage : PropTypes.func.isRequired,
  msgNull : PropTypes.bool.isRequired,
  added_height : PropTypes.number.isRequired,
}

export default MessageInputAuth;
