import React from 'react'
import {View} from 'react-native'
import Ptext from "../../../common/Ptext.js"
import { NavLink } from '../../router/react-router';

export default function ServiceIcon({ title = 'Service', style = {}, active = false, onPress = null  })
{
  return (
    <NavLink to="/services" style={style}
        activeStyle={{
        fontWeight: "bold",
        color: '#1180AF'
      }}>
        {title}
    </NavLink>
  )
}
