class ContactList extends Component{
  constructor(props) {
    super(props);

    this.state = {
      selected : (new Map() : Map<string,boolean>),
      filter : 3,
      arrayHolder : [],
      data : [],
    };

    this.props.misago.do_fetch_public_threads(this._setNewData);
  }

  _setNewData = (data) => {
    this.setState({data : data});
  }

  _keyExtractor = (item,index) => index.toString();

  _onPressItem = (index : int) => {
    this.setState((state)=> {
        const selected = (new Map() : Map<string,boolean>);
        selected.set(index,!selected.get(index));
        return {selected};
    }
    );
    if (this.props.showChat)
      this.props.showChat(this.state.data[index]);
  }
  _changeFilter = (value) => {
    this.setState({filter : value});
  }
  _changeTextFilter = (value) => {
    const textData = value.toUpperCase();
    const newData = this.state.arrayHolder.filter(
      item => {
          const itemText = item.chatName.toUpperCase()+ ' ' + item.lastMessage.toUpperCase();
          console.log(itemText);
          return itemText.indexOf(textData) > -1;
      }
    );
    this.setState({data : newData});
  }

  _renderHeader = () => {
      return (
        <ContactListHeader selectChange={this._changeFilter} showCreateChat={this.props.showCreateChat}
          onSearchText={this._changeTextFilter}/>
      )
  }

  render () {
    return (
      <FlatList
        data={this.state.data}
        style={[styles.wrapperList,{width : this.props.fullWidth,height : this.props.fullHeight}]}
        renderItem={({item,index}) => {
          if (item.type & this.state.filter) {
          return (
            <ContactItem chat={item} id={index} key={index}
            selected={!!this.state.selected.get(index)}
            onPressItem={this._onPressItem}/>
          )}
          else {
            return null;
          }
        }}
        keyExtractor={this._keyExtractor}
        ListHeaderComponent={this._renderHeader}
        stickyHeaderIndices={[0]} /* sticky list header to top */
      />

    )
  }


}

const styles = StyleSheet.create({
  wrapperList : {
    width : '100%',
    backgroundColor : '#fff',
  }

});

export default ContactList;
