import React, { Component } from 'react';
import { View, StyleSheet, TouchableOpacity,Image } from 'react-native';
import Svg from 'react-native-svg';
import Ptext from "../../../common/Ptext.js"

class MessageListHeader extends Component {

  render () {
      /* Back button Chat label, kebab menu button*/
    return (

        <View style={styles.horizontalWrapper}>
          <TouchableOpacity style={styles.buttonKebab} onPress={this.props.showChatList}>
            <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path fillRule="evenodd" clipRule="evenodd" d="M15.4 7.4L14 6L8 12L14 18L15.4 16.6L10.8 12L15.4 7.4Z" fill="black"/>
            </Svg>
          </TouchableOpacity>
          <Image style={styles.chatAvatar} source={this.props.chat.avatar_url}/>
          <TouchableOpacity style={styles.labelBox}>
          <Ptext style={styles.label}>{this.props.chat.chatName}</Ptext>
          </TouchableOpacity>
          <TouchableOpacity style={styles.buttonKebab}>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="12" cy="6" r="2" fill="#B4BBC2"/>
            <circle cx="12" cy="12" r="2" fill="#B4BBC2"/>
            <circle cx="12" cy="18" r="2" fill="#B4BBC2"/>
            </svg>
          </TouchableOpacity>
        </View>
    )
  }
}

const styles = StyleSheet.create({
  horizontalWrapper : {
    flexDirection : 'row',
    width : '100%',
    justifyContent : 'flex-start',
    alignSelf : 'center',
    alignItems : 'center',
    height : 48,
    backgroundColor : '#fff',
  },
  label : {
    fontSize : 16,
    lineHeight : 24,
    fontWeight : 'bold',
    flex : 2,
    textAlign : 'left',
    marginLeft : 17,
  },
  labelBox : {
    flex : 2,
    marginLeft : 17,
  },
  buttonKebab : {
    width : 32,
    height : 32,
    justifyContent : 'center',
    alignItems : 'center',
    marginLeft : 6,
    marginRight : 12,
  },
  chatAvatar : {
    width : 32,
    height : 32,
  },
});

export default MessageListHeader;
