import React,{Component} from 'react'
import {View,StyleSheet} from 'react-native'
import {Text} from 'react-native';

class TabReview extends Component {

  render () {
    return (
      <View style={styles.wrapper}>
      <Text style={{alignSelf : 'center'}}>Еще никто не оставил отзывы</Text>
      <Text/>
      <Text/>
      <Text  style={{alignSelf : 'center'}}>Скорее всего, этот специалист только приступил к работе с клиентами. Не сомневайтесь в его квалификации. Всех экспертов я обучаю лично и они опытны в разборе матриц. Качество гарантирую.</Text>
      </View>
    )
  }
}

const styles = StyleSheet.create({
  wrapper : {
    width : '95%',
    alignSelf : 'center',
    alignItems : 'flex-start',
    justifyContent : 'flex-start',
    flex : 1,
  }

});

export default TabReview;
