import React, { Component } from 'react';
import { View, StyleSheet,TouchableOpacity } from 'react-native';
import Ptext from "../../../common/Ptext.js"
import Svg from 'react-native-svg'

class ActionNotify extends Component {
  render () {
      return (
        <TouchableOpacity onPress={this.props.onPress}>
          <View style={styles.actionColumn}>
            <View style={styles.actionColumnIcon}>
              <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M16.2353 16.8063H7.76471V10.3903C7.76471 7.92253 9.64706 5.94835 12 5.94835C14.3529 5.94835 16.2353 7.92253 16.2353 10.3903V16.8063ZM18.1176 15.8192V10.3903C18.1176 7.35989 16.1035 4.82307 13.4118 4.15185V3.48063C13.4118 3.08794 13.263 2.71134 12.9983 2.43367C12.7335 2.15599 12.3744 2 12 2C11.6256 2 11.2665 2.15599 11.0017 2.43367C10.737 2.71134 10.5882 3.08794 10.5882 3.48063V4.15185C7.88706 4.82307 5.88235 7.35989 5.88235 10.3903V15.8192L4 17.7934V18.7805H20V17.7934L18.1176 15.8192ZM12 21.7418C12.4992 21.7418 12.978 21.5338 13.331 21.1635C13.684 20.7933 13.8824 20.2912 13.8824 19.7676H10.1176C10.1176 20.2912 10.316 20.7933 10.669 21.1635C11.022 21.5338 11.5008 21.7418 12 21.7418Z" fill="black"/>
              </Svg>
            </View>
            <View style={styles.actionLabel}>
              <Ptext style={styles.actionCaptionLabel}>
              Уведомления
              </Ptext>
              {this.props.eventsCnt !== 0 ?
              <View style={styles.actionEventsLabel}>
              <Ptext style={styles.actionEventsText}>
              {this.props.eventsCnt}
              </Ptext>
              </View> : null }
            </View>
          </View>
        </TouchableOpacity>
      )
    }
}

const styles = StyleSheet.create({
  actionColumn : {
    width : '100%',
    height : 48,
    flexDirection : 'row'
  },
  actionColumnIcon : {
    width : 64,
    paddingLeft : 24,
    paddingTop : 12,
  },
  actionLabel : {
    flex : 1,
    flexDirection : 'row',
    alignItems : 'center',
    justifyContent : 'flex-start',
    paddingLeft : 5
  },
  actionCaptionLabel : {
    fontSize : 16
  },
  actionEventsView : {
    flex : 1,
  },
  actionEventsText : {
    borderRadius : 10,
    borderWidth : 1,
    borderColor : '#45B4E3',
    color : '#45B4E3',
    borderStyle : 'solid',
    textAlign : 'left',
    paddingLeft : 8,
    paddingRight : 8,
    marginLeft : 8,
    marginRight : 8,
    fontSize : 16
  }
})
export default ActionNotify;
