import React from 'react'
import {View} from 'react-native'
import Ptext from "../../../common/Ptext.js"
import { TouchableOpacity } from 'react-native';

export default function SchoolIcon({ size = 40, title = 'School', style = {} , active = false, onPress = null, fontSize = 14  })
{
  return (
  <TouchableOpacity onPress={onPress} style={style}>
    <View style={[style,{width : '100%'}]}>
        <Ptext fontSize={ fontSize } style={{color : active ? '#1180AF' : '#000',fontWeight : active ? 'bold' : 'normal'}}>
          {title}
        </Ptext>
    </View>
  </TouchableOpacity>
  )
}
