import React, { Component } from 'react';
import { StyleSheet, TextInput , View } from 'react-native';
import Button from "../../../common/Button.js"

class ItemNameInput extends Component {
  constructor(props) {
    super(props);
    this.state={};

  }

  componentDidMount() {
		// isValid method returns if the inputed value is valid.
		// Ex: if you input 40/02/1990 30:20:20, it will return false
		//	   because in this case, the day and the hour is invalid.

		// get converted value. Using type=datetime, it returns the moment object.
		// If it's using type=money, it returns a Number object.
	}

  debugPress = () => {
    if (this.props.cb.cbSavePress !== undefined)
      this.props.cb.cbSavePress();
  }

  render() {
  		// the type is required but options is required only for some specific types.
  		return (
        <View style={this.props.style}>
          <TextInput placeholder={'Введите имя'} style={styles.textinput} onChangeText={this.props.cb.cbNameInput} value={this.props.cb.value}/>
          <Button title='Сохранить' onPress={this.debugPress} style={styles.button}/>
        </View>
      )
  	}
}

const styles = StyleSheet.create({
  textinput : {
    textAlign : 'left',
    margin : 0,
    minWidth : 107,
    backgroundColor : '#fff',
    flex : 1,
    borderRadius : 4,
    paddingLeft : 4,
    borderColor : '#fff'
  },
  button : {
    backgroundColor : '#fff',
    borderColor : '#b4bbc2',
    margin : 0,
    marginTop : 0,
    marginBottom : 0,
    minWidth : 147,
  }
})

export default ItemNameInput;
