import React from 'react'
import { StyleSheet } from 'react-native'
import Svg, { G } from 'react-native-svg'
import { TouchableOpacity } from 'react-native';

export default function MenuIcon({ style = {}, notify = false, onPress = null  })
{
  return (
    <TouchableOpacity style={[styles.icon, style]} onPress={onPress}>
      <Svg width="29" height="24" viewBox="0 0 29 24" fill="none" xmlns="http://www.w3.org/2000/svg" >
      <G >
        <rect x="4" y="6" width="16" height="2" rx="1" fill="black"/>
        <rect x="3" y="11" width="18" height="2" rx="1" fill="black"/>
        <rect x="4" y="16" width="16" height="2" rx="1" fill="black"/>
        {notify ? <circle cx="26" cy="3" r="3" stroke="#45B4E3" fill="#45B4E3"/> : null}
      </G>
      </Svg>
  </TouchableOpacity>
  )
}

const styles = StyleSheet.create({
  icon: {
    height: 20,
    width : '100%',
    alignItems: 'flex-start',
    backgroundColor : '#fff',
  }
})
