import React, { Component } from 'react';
import { StyleSheet,View } from 'react-native';
import { SegmentedButton } from '@kiwicom/universal-components'


class ItemGenderSelector extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.state.segmentsData = [
      {
        label : 'Муж',
        value : 'male'
      },
      {
        label : 'Жен',
        value : 'female'
      }
    ];
    this.state.selectedValue = 'male';
    this.state.onValueChange = this.onValueChange.bind(this);
  }

  onValueChange(a){
    this.setState({selectedValue :a});
    if (this.props.onChange !== undefined)
      this.props.onChange(a);
  }

  render() {
    return (
      <View style={[styles.genderselector,this.props.style]}>
      <SegmentedButton segmentsData={this.state.segmentsData}
        selectedValue={this.state.selectedValue}
        onValueChange={this.state.onValueChange} />
      </View>
    )
  }

}

const styles = StyleSheet.create({
  genderselector : {
    minWidth : 136,
    height : 40,
  }
});

export default ItemGenderSelector;
