import React from 'react'
import { StyleSheet } from 'react-native'
import Svg, { G } from 'react-native-svg'
import { TouchableOpacity } from 'react-native';

export default function MessagesIcon({ style = {}, notify = false, onPress = null  })
{
  return (
    <TouchableOpacity onPress={onPress} style={[ styles.icon, style ]}>
      <Svg width="24" height="22" viewBox="0 0 24 22" fill="none" xmlns="http://www.w3.org/2000/svg" >
        <G>
        <path transform="translate(0,6)" d="M1.8 0H16.2C16.6774 0 17.1352 0.198893 17.4728 0.552925C17.8104 0.906957 18 1.38713 18 1.8878V13.2146C18 13.7153 17.8104 14.1955 17.4728 14.5495C17.1352 14.9035 16.6774 15.1024 16.2 15.1024H1.8C0.801 15.1024 0 14.2529 0 13.2146V1.8878C0 0.840073 0.801 0 1.8 0ZM9 6.60732L16.2 1.8878H1.8L9 6.60732ZM1.8 13.2146H16.2V4.12485L9 8.83493L1.8 4.12485V13.2146Z" fill="black"/>
        {notify ? <circle cx="21" cy="4" r="3" stroke="#45B4E3" fill="#45B4E3"/> : null}
        </G>
      </Svg>
    </TouchableOpacity>
  )
}

const styles = StyleSheet.create({
  icon: {
    height: 20,
    width : '100%',
    alignItems: 'flex-start',
    backgroundColor : '#fff',
  }
})
