const styles = StyleSheet.create({
  wrapper : {
    width : '95%',
    alignSelf : 'center',
    alignItems : 'flex-start',
    justifyContent : 'flex-start',
  },
  button : {
    backgroundColor : '#fff',
    borderColor : '#b4bbc2',
    margin : 0,
    marginTop : 16,
    minWidth : 95,
    height : 32,
  }

});


export default TabAbout;
