import React, { Component } from 'react';
import { View, StyleSheet } from 'react-native';
import ExpertItem from "./expertItem.js"


class ExpertList extends Component {


  render () {
    return (
      <View style={styles.wrapper}>
        {
          this.props.info.map((item,key,arr)=>{
            return (
                <ExpertItem
                  info={item}
                  key={key}
                  onSelect={this.props.onSelect.bind(this,key)}
                  onAskQuestion={this.props.onAskQuestion.bind(this,key)}
                  onExtendedInfo={this.props.onExtendedInfo.bind(this,key)}
                  />
            );
          })
        }
      </View>
    )
  }
}

const styles = StyleSheet.create({
  wrapper : {
    flexDirection : 'column',
    width : '90%',
    backgroundColor : '#fffff',
  },
});

export default ExpertList;
