import React,{ Component } from 'react';
import {FlatList,View} from 'react-native';
import BlogMozaikItem from './blogMozaikItem.js';
import Stories from "../stories/stories.js"
import BlogTopHeader from "./blogTopHeader.js"
import BlogSecondHeader from "./blogSecondHeader.js"

class BlogMozaikList extends Component{


 rendItem = ({item,index}) => {
    return (
      <BlogMozaikItem
        key={index}
        item={item}
        id={index}
        openComments={this.props.openComments}
        showFullPost={this.props.showFullPost}
        scrollWidth={this.props.scrollWidth}/>
    )
 }

 renderHeader = () =>{
   return (
     <View style={{alignItems:'center'}}>
       <BlogTopHeader search={this.props.search}/>
       <Stories items={this.props.stories}/>
       <BlogSecondHeader viewChanged={this.props.viewChanged}
       currentView={this.props.currentView}/>
     </View>
   )
 }

  render()  {
    return(
      <FlatList
        data={this.props.data}
        renderItem={this.rendItem}
        numColumns={3}
        keyExtractor={(item, index) => index.toString()}
        ListHeaderComponent={this.renderHeader}
        style={{height:this.props.t_height,width:this.props.scrollWidth}}
      />
    )
  }
}

export default BlogMozaikList;
