// this class must be inited by all services from server ( )

class ServiceContainer extends Component {
  constructor(props){
    super(props);
    this.state = {
      show : 0,
      key : -1,
      showHint : true,
      selectedMain : true,
      showHeader : true,
      selExpert : 0,
      selectManual : false,
      lastShow : -1,
      askId : -1,
      urgent : false,
    };
  }

  onItemSelect = (key) => {
    console.log('Selected item '+key);
    this.setState({show : 1, key : key});
  }
  onFormFillNextStage = () => {
    this.setState({show : this.state.show+1});
  }
  backPressed = () => {
    let c = this.state.show;
    if (c > 0)
      c--;
    if (this.state.lastShow !== -1)
      c = this.state.lastShow;
    this.setState({show : c,
      lastShow : -1});
  }
  onCloseHint = () => {
    this.setState({showHint : false});
  }
  onChangeMainExpert = () => {
    this.setState({selectedMain : !this.state.selectedMain});
  }
  onAskQuestion = (id) => {
    console.log('Ask question with peer '+id);
    this.setState({
      lastShow : this.state.show,
      show : 6,
      askId : id,
    });
  }
  onExtInfoShow = (id) => {
    console.log('Show more info about peer '+id);
  }
  onHideHeader = (state) => {
    this.setState({showHeader : !state});
  }
  onChangeExpert = (key) => {
    this.setState({selExpert : key,
    showHeader : true,
    show : this.state.show-1,
    });
  }
  onChangeToExpertChoose = () => {
    this.setState({show : this.state.show+1,
      selectManual : true
    });
  }

  onChangeDate = () => {
    console.log('Show change date calendar');
  }

  onUrgentCheck = () => {
    console.log('Urgent Checked');
    this.setState({urgent : !this.state.urgent});
  }

  onFormToPay = () => {
    console.log('Show pay form');
  }
  reqConfirm = () => {
    this.setState({emailConfirm : true});
  }

  render () {
    let serv = this.state.selectedMain ? this.props.serv_o : this.props.serv_e;
    if (serv === undefined) serv = [];
    let w_size_c = this.props.scrollWidth*0.9;
    return (
      <ScrollView style={{
            width:w_size_c,
            flexDirection : 'column',
            alignSelf : 'center',
            height:this.props.t_height,
            }}
            contentContainerStyle={{
              width:w_size_c,
              alignSelf : 'center'
            }}

            >
      {this.state.show === 0 && this.state.showHint ?
        <ServiceNotify onClose={this.onCloseHint}/>
        :
        null
      }
      {this.state.show === 0 ?
        <ServiceSelector selected={this.state.selectedMain} style={{width:'100%'}}
          onChange={this.onChangeMainExpert}
          onAskQuestion={this.onAskQuestion}
          onExtInfoShow={this.onExtInfoShow}/>
        :
        null
      }
      {this.state.show === 0 ?
        <ServiceList services={serv} style={{width:'100%'}}
          onSelect={this.onItemSelect}/>
        :
        null
      }
      {this.state.show > 0 && this.state.showHeader ?
        <ServiceStageHeader stage={this.state.show} style={{width:'100%'}}
        onBackPressed={this.backPressed}
        />
      :
        null
      }
      {this.state.show === 1 ?
        <ServiceApplyForm count={serv[this.state.key].persCount} style={{width:'100%'}}
          onNextStage={this.onFormFillNextStage}/>
        :
        null
      }
      {
        this.state.show === 2 ?
        <ServiceSelectExpert selectedId={this.state.selExpert} experts={experts}
          expertMain={experts_olga}
          style={{width:'100%'}}
          main={this.state.selectedMain}
          onExpertChoose={this.onChangeToExpertChoose}
          onNextStage={this.onFormToPay}
          manual={this.state.selectManual}
          changeDate={this.onChangeDate}
          urgent={this.state.urgent}
          urgentCheck={this.onUrgentCheck}
          reqConfirm={this.reqConfirm}
          emailConfirm={this.state.emailConfirm}
          />
        :
        null
      }
      {
        this.state.show === 3 ?
        <ExpertContainer experts={experts}
          onExpertChoose={this.onFormFillNextStage}
          onSelect={this.onChangeExpert}
          onAskQuestion={this.onAskQuestion}
          onHideHeader={this.onHideHeader}
          />
        :
        null
      }


      {
        this.state.show === 6 ?
        <ContactContainer to={experts[this.state.askId]}
          onSubmit={this.backPressed}
          style={{width:'100%'}}
          />
        :
        null
      }
      </ScrollView>
    )
  };


}

export default ServiceContainer;
