import React,{ Component } from 'react';
import { StyleSheet,TouchableOpacity } from 'react-native';
import Ptext from "../../common/Ptext.js"
import Svg from 'react-native-svg';

let stages = [
  {
    title : 'СТРАННОЕ МЕСТО',
  },
  {
    title : 'Блог',
  },
  {
    title : 'Комментарии',
  },
];

class BlogPostStageHeader extends Component {


  render () {
    return (
      <TouchableOpacity onPress={this.props.onBackPressed} style={[styles.wholeForm,this.props.style]}>
        <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path fill-rule="evenodd" clip-rule="evenodd" d="M15.4 7.4L14 6L8 12L14 18L15.4 16.6L10.8 12L15.4 7.4Z" fill="black"/>
        </Svg>
      <Ptext style={styles.title}>{stages[this.props.stage].title}</Ptext>
      </TouchableOpacity>
    )
  }
}

const styles = StyleSheet.create({
  wholeForm : {
    flexDirection : 'row',
    width : '100%',
    justifyContent : 'flex-start',
    alignItems : 'center',
    alignSelf : 'center',
    minHeight : 80,
  },
  icon : {
    marginLeft : 20,
    flexDirection : 'column',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
  },
  title : {
    textAlign : 'left',
    marginTop : 8,
    marginLeft : 16,
    marginBottom : 4,
    fontSize : 24,
    fontWeight : 'bold',
    flex : 1,
  },

});


export default BlogPostStageHeader;
