import React, { Component } from 'react';
import { View, Image, StyleSheet,TouchableOpacity,TextInput } from 'react-native';
import Ptext from "../../common/Ptext.js"
import Svg from 'react-native-svg';

class ReplyComment extends Component {

  render () {
    return(

        <View style={styles.wrapperHorizontal}>
          <Image source={this.props.comment.avatar} style={{width:24,height:24,marginLeft : 4, marginRight : 4}}/>
          <View style={styles.wrapperVertComment}>
            <View style={[styles.wrapperHorizontal,{width : '100%', marginBottom : 0}]}>
              <Ptext style={styles.username}>{this.props.comment.name}</Ptext>
              <Ptext style={styles.time}>{this.props.comment.time}</Ptext>
            </View>
            <Ptext style={styles.comment}>{this.props.comment.text}</Ptext>
            <View style={[styles.wrapperHorizontal,{width : '100%', marginTop : 0}]}>
              <Svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M8.06659 12.3667L7.99992 12.4333L7.92659 12.3667C4.75992 9.49333 2.66659 7.59333 2.66659 5.66667C2.66659 4.33333 3.66659 3.33333 4.99992 3.33333C6.02659 3.33333 7.02659 4 7.37992 4.90667H8.61992C8.97325 4 9.97325 3.33333 10.9999 3.33333C12.3333 3.33333 13.3333 4.33333 13.3333 5.66667C13.3333 7.59333 11.2399 9.49333 8.06659 12.3667ZM10.9999 2C9.83992 2 8.72659 2.54 7.99992 3.38667C7.27325 2.54 6.15992 2 4.99992 2C2.94659 2 1.33325 3.60667 1.33325 5.66667C1.33325 8.18 3.59992 10.24 7.03325 13.3533L7.99992 14.2333L8.96659 13.3533C12.3999 10.24 14.6666 8.18 14.6666 5.66667C14.6666 3.60667 13.0533 2 10.9999 2Z" fill="black"/>
              </Svg>
              <Ptext style={styles.liked_text}>{this.props.comment.cntLike} нравится</Ptext>
              <TouchableOpacity>
                <Ptext style={styles.liked_text}>Ответить</Ptext>
              </TouchableOpacity>
            </View>
          </View>
        </View>

    )
  }
}

class RootComment extends Component {

  render () {
    return(

        <View style={styles.wrapperHorizontal}>
          <Image source={this.props.comment.avatar} style={{width:24,height:24,marginLeft : 4, marginRight : 4}}/>
          <View style={styles.wrapperVertComment}>
            <View style={[styles.wrapperHorizontal,{width : '100%', marginBottom : 0}]}>
              <Ptext style={styles.username}>{this.props.comment.name}</Ptext>
              <Ptext style={styles.time}>{this.props.comment.time}</Ptext>
            </View>
            <Ptext style={[styles.comment,{marginBottom : 6}]}>{this.props.comment.text}</Ptext>
            <View style={[styles.wrapperHorizontal,{width : '100%', marginTop : 0}]}>
              <Svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M8.06659 12.3667L7.99992 12.4333L7.92659 12.3667C4.75992 9.49333 2.66659 7.59333 2.66659 5.66667C2.66659 4.33333 3.66659 3.33333 4.99992 3.33333C6.02659 3.33333 7.02659 4 7.37992 4.90667H8.61992C8.97325 4 9.97325 3.33333 10.9999 3.33333C12.3333 3.33333 13.3333 4.33333 13.3333 5.66667C13.3333 7.59333 11.2399 9.49333 8.06659 12.3667ZM10.9999 2C9.83992 2 8.72659 2.54 7.99992 3.38667C7.27325 2.54 6.15992 2 4.99992 2C2.94659 2 1.33325 3.60667 1.33325 5.66667C1.33325 8.18 3.59992 10.24 7.03325 13.3533L7.99992 14.2333L8.96659 13.3533C12.3999 10.24 14.6666 8.18 14.6666 5.66667C14.6666 3.60667 13.0533 2 10.9999 2Z" fill="black"/>
              </Svg>
              <Ptext style={styles.liked_text}>{this.props.comment.cntLike} нравится</Ptext>
              <TouchableOpacity>
              <Ptext style={styles.liked_text}>Ответить</Ptext>
              </TouchableOpacity>
            </View>
            {
              this.props.comment.reply.map((comment,key,arr)=>{
                  return (
                    <ReplyComment key={key} id={key} comment={comment}/>
                  )
                }
              )
            }
          </View>
        </View>

    )
  }
}


class CommentsFull extends Component {


  render () {
    return (
      <View style={styles.wrapper}>
      {this.props.comments.map((comment,key,arr) => {
          return (
              <RootComment key={key} id={key} comment={comment}/>

          );
        })
      }
      <View style={{flexDirection : 'row',width : '100%',height : 48, alignItems : 'center'}}>
        <TextInput placeholder={'Введите имя'} style={styles.textinput} />
        <TouchableOpacity>
          <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M22 12C22 14.6522 20.9464 17.1957 19.0711 19.0711C17.1957 20.9464 14.6522 22 12 22C10.6868 22 9.38642 21.7413 8.17317 21.2388C6.95991 20.7362 5.85752 19.9997 4.92893 19.0711C3.05357 17.1957 2 14.6522 2 12C2 9.34784 3.05357 6.8043 4.92893 4.92893C6.8043 3.05357 9.34784 2 12 2C13.3132 2 14.6136 2.25866 15.8268 2.7612C17.0401 3.26375 18.1425 4.00035 19.0711 4.92893C19.9997 5.85752 20.7362 6.95991 21.2388 8.17317C21.7413 9.38642 22 10.6868 22 12ZM6 13H14L10.5 16.5L11.92 17.92L17.84 12L11.92 6.08L10.5 7.5L14 11H6V13Z" fill="#B4BBC2"/>
          </Svg>
        </TouchableOpacity>
      </View>
      </View>
    )
  }
}

const styles = StyleSheet.create({
  wrapper : {
    flexDirection : 'column',
    flex : 1,
    alignSelf : 'flex-start',
    alignItems : 'flex-start',
    justifyContent : 'flex-start',
    marginTop : 0,
    paddingTop : 30,
    backgroundColor : '#fff',
  },
  wrapperVertical : {
    flexDirection : 'column',
    alignItems : 'flex-start',
    justifyContent : 'flex-start',
  },
  wrapperHorizontal : {
    flexDirection : 'row',
    alignItems : 'flex-start',
    justifyContent : 'flex-start',
    marginBottom : 14,
    flex : 1,

  },
  wrapperVertComment : {
    flexDirection : 'column',
    alignItems : 'flex-start',
    justifyContent : 'flex-start',
    flex : 1,
  },
  wrapperVertComment2ndLevel : {
    flexDirection : 'column',
    alignItems : 'flex-start',
    justifyContent : 'flex-start',
    flex : 1,
    borderLeftWidth : 1,
    borderColor : '#B4BBC2',
    paddingLeft : 12,
  },
  username : {
    fontSize : 14,
    lineHeight : 16,
    textAlign : 'left',
    fontWeight : 'bold',
    flex : 1,
    width : '80%',
  },
  time : {
    fontSize : 14,
    lineHeight : 16,
    color : '#B4BBC2',
    textAlign : 'right',
  },
  liked_text : {
    fontSize : 12,
    lineHeight : 16,
    color : '#B4BBC2',
    textAlign : 'left',
    marginLeft : 20,
  },
  comment : {
    fontSize : 14,
    lineHeight : 16,
    color : '#000',
    textAlign : 'left',
  },
  textinput : {
    height : 24,
    flex : 1,
    textAlign : 'left',
    marginLeft : 16,
    backgroundColor : '#fff',
    borderRadius : 4,
    borderColor : '#fff',
  },
  vertLine : {
    borderColor : '#B4BBC2',
    width : 1,
    borderWidth : 1,
    marginRight : 12,
    height : '100%',
  },
  allcomments : {
    fontSize : 14,
    lineHeight : 16,
    color : '#B4BBC2',
    textAlign : 'left',
    alignSelf : 'flex-start',
    marginBottom : 18,
  }
});

export default CommentsFull;
