import React,{ Component } from 'react';
import { View, StyleSheet, TextInput } from 'react-native';
import ItemDateInput from '../../common/dateinput.js'
import ItemGenderSelector from '../../common/genderselector.js'
import moment from 'moment';


class ServiceFormPersonInput extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date : props.date,
      gender : 'male',
    }
  }

  genderValueChanged = (gender) => {
    this.setState({gender : gender});
    if (this.props.genderEntered)
      this.props.genderEntered(gender);
  }

  date_valid = ( value ) => {

    //TODO: check to string type
    return moment(value,["DD.MM.YYYY","D.MM.YYYY","DD.M.YYYY","D.M.YYYY"],true).isValid();
  }

  dateInputChanged = (value) => {
    let ppp = value.replace(/[^\d]/g, '');
    let days = ppp.slice(0,2);
    let mm = ppp.slice(2,4);
    let yy = ppp.slice(4);

    if (parseInt(days,10) > 31)
      days='31';
    if ((days.length)&&(days[0] > '3'))
        days = ("000" + parseInt(days,10)).slice(-2);

    if (parseInt(mm,10) > 12)
        mm='12';
    if ((mm.length)&&(mm[0]>'1'))
        mm = ("000" + parseInt(mm,10)).slice(-2);

    let tt = [];
    if (days.length)
      tt.push(days);
    if (mm.length)
        tt.push(mm);
    if (yy.length)
        tt.push(yy);
    ppp = tt.join('.');



    this.setState({date : ppp});
    if (ppp.length > 7) {
      if (this.date_valid(ppp))
        this.props.dateEntered(ppp);
    }
  }


  render () {
    return (
      <View style={styles.wrapper}>
        <ItemDateInput style={styles.dateInput}
          onChangeText={this.dateInputChanged}
          bidValue={this.state.date}/>
        <ItemGenderSelector style={styles.genderInput} onChange={this.genderValueChanged}/>
        <TextInput placeholder={'Имя'} style={styles.nameInput} onChangeText={this.props.nameEntered}/>
      </View>
    )
  }

};

const styles = StyleSheet.create({
  wrapper : {
    width : '100%',
    flexDirection : 'row',
    flexWrap : 'wrap',
  },
  dateInput : {
    width : 119,
    marginTop : 8,
    marginBottom : 8,
    marginLeft : 8,
  },
  genderInput : {
    minWidth : 136,
    margin : 8,
  },
  nameInput : {
    minWidth : 208,
    borderWidth : 1,
    borderRadius : 4,
    borderColor : '#B4BBC2',
    margin : 8,
    padding : 4,
    minHeight : 40,
    flex : 1,
  }
});

export default ServiceFormPersonInput;
