import React,{Component} from 'react';
import {View,  StyleSheet} from 'react-native';
import ServiceItem from "./serviceItem"

/*
  services = [
    service
  ]


  service = {
    id,
    descr,
    price,
    currency,
    name
  }
*/

class ServiceList extends Component {
  render () {
    return (
      <View style={[styles.serviceList,this.props.style]}>
        {
          this.props.services.map((service,key,arr) => {
            return (
                <ServiceItem moreTitle={'Что включает описание'} key={key} id={key} service={service} onSelect={this.props.onSelect} />
            );
          })
        }
      </View>
    )
  }
}
// TODO change list to increase height only one item in list
const styles = StyleSheet.create({
    serviceList : {
      width : '100%',
      flexDirection : 'row',
      flexWrap : 'wrap',
      justifyContent : 'space-around',
      alignItems : 'center',
      borderRadius : 4,
    }
});

export default ServiceList;
