import React,{Component} from 'react';
import {View, StyleSheet} from 'react-native';
import ExpertList from '../../components/expert/expertList.js';
import ExpertExtInfo from '../../components/expert/expertExtInfo.js';


// this class must be inited by all services from server ( )

class ExpertContainer extends Component {
  constructor(props){
    super(props);
    this.state = {
      listActive : true,
      showKey : 0
    };
  }

  onExtendedInfo = (key) => {
    this.props.onHideHeader(true);
    this.setState({listActive : false, showKey : key});
  }

  onAskQuestion = (key) => {
    console.log('Pressed onAskQuestion '+key);
    this.props.onAskQuestion(key);
  }

  onSelect = (key) => {
    this.props.onSelect(key);
  }

  onBack = () => {
    this.props.onHideHeader(false);
    this.setState({listActive : true, showKey : 0});
  }

  render () {
    return (
        <View style={styles.container}>
        {this.state.listActive ?
          <ExpertList info={this.props.experts}
            onAskQuestion={this.onAskQuestion}
            onSelect={this.onSelect}
            onExtendedInfo={this.onExtendedInfo}
            />
            :
            <ExpertExtInfo info={this.props.experts[this.state.showKey]}
              onAskQuestion={this.onAskQuestion.bind(this,this.state.showKey)}
              onSelect={this.onSelect.bind(this,this.state.showKey)}
              onCbBack={this.onBack}
              />
          }
        </View>
    )
  };


}

const styles=StyleSheet.create({
  container : {
    backgroundColor : '#ffffff',
  }
});

export default ExpertContainer;
