import React, { Component } from 'react';
import { View, StyleSheet } from 'react-native';
import { Dimensions } from 'react-native';
import OneCell from "./MatrixItems/oneCell.js"
import MatrixCells from "./MatrixItems/matrixCells.js"
import ThreeColumn from "./MatrixItems/threeColumn.js"
import MatrixInfo from "./MatrixItems/matrixInfo.js"
import ItemNameInput from "./Formitems/nameInput.js"





class MatrixView extends Component
{
    state = {
      dims : {},
      cells : {
        temp: {
          value : 0,
          descr : 'Темперамент'
        },
        house : {
          value : 0,
          descr : 'Быт'
        },
        threeColumn : [
            {
              value : 0,
              descr : 'Цель'
            },
            {
              value : 0,
              descr : 'Семья'
            },
            {
              value : 0,
              descr : 'Привычки'
            }
        ],
        matrix : [
            {
              value : 0,
              descr : 'Характер'
            },
            {
              value : 0,
              descr : 'Здоровье'
            },
            {
              value : 0,
              descr : 'Удача'
            },
            {
              value : 0,
              descr : 'Энергия'
            },
            {
              value : 0,
              descr : 'Логика'
            },
            {
              value : 0,
              descr : 'Долг'
            },
            {
              value : 0,
              descr : 'Интерес'
            },
            {
              value : 0,
              descr : 'Труд'
            },
            {
              value : 0,
              descr : 'Память'
            }
        ],
      lucky : '',
      support_nums : [
        0,0,0,0 ]
      }
    };
    constructor(props){
      super(props);
      this.dims_changed({window : Dimensions.get("window")});
    }

    componentWillMount() {
          Dimensions.addEventListener("change", this.dims_changed);
    }

    componentWillUnmount() {
      // Important to stop updating state after unmount
        Dimensions.removeEventListener("change", this.dims_changed);
    }

    dims_changed = dims => {
    }

    render() {
      let temp_cell = {...this.state.cells.temp,...this.props.cells.temp};
      let matrix = this.state.cells.matrix;
      let i = 0;
      for (i = 0; i < matrix.length; i++)
        if (this.props.cells.matrix !== undefined)
          matrix[i].value = this.props.cells.matrix[i].value;

      let threeCells = this.state.cells.threeColumn;
      for (i = 0; i < threeCells.length; i++)
        if (this.props.cells.threeCells !== undefined)
          threeCells[i].value = this.props.cells.threeCells[i].value;

      let house_cell = {...this.state.cells.house,...this.props.cells.house};

      return (
        <View style={[styles.wrapperColumn,this.props.style]}>
          <View style={styles.wrapperRow}>
          <MatrixInfo style={styles.wrapperMatrixInfo} lucky={this.props.cells.lucky}
             support_nums={this.props.cells.support_nums}/>
          <OneCell style={styles.oneCellLeft} cell={temp_cell}/>
          </View>
          <View style={styles.wrapperRow}>
            <View style={[styles.wrapperColumnMatrix]} >
              <MatrixCells cells={matrix} style={{width:'100%'}}/>
              <OneCell cell={house_cell} style={{width:'33.3%'}}/>
            </View>
            <ThreeColumn cells={threeCells} style={{width:'25%'}}/>
          </View>
          <ItemNameInput style={styles.nameInput}  cb={this.props.cb}/>
        </View>
      )
    }
};


const styles = StyleSheet.create({
  wrapperRow : {
    flexDirection : 'row',
    width : '100%',
    alignItems : 'flex-start',
    justifyContent : 'flex-end'
  },
  oneCellLeft : {
    width : '25%'
  },
  wrapperColumn : {
    flexDirection : 'column',
    justifyContent : 'flex-end',
    alignItems : 'flex-end',
    width : '90%'
  },
  wrapperColumnMatrix : {
    flexDirection : 'column',
    justifyContent : 'center',
    alignItems : 'center',
    width : '75%'
  },
  wrapperMatrixInfo : {
    width : '75%',
    padding : 20
  },
  nameInput : {
    flexDirection : 'row',
    width : '100%',
    justifyContent : 'center',
    alignContent : 'flex-start',
    padding : 0,
    borderRadius : 4,
    borderWidth : 1,
    borderColor : '#B4BBC2',
    marginBottom : 8
  }

});

export default MatrixView;
