import React,{ Component } from 'react';
import { View, StyleSheet, TouchableOpacity } from 'react-native';
import Svg from 'react-native-svg'
import Ptext from "../../common/Ptext.js"


class ServiceNotify extends Component {


  render () {
    return (
      <View style={styles.wholeForm}>
        <View style={styles.header}>
          <Ptext style={styles.headerTitle}>Дорогие друзья!</Ptext>
          <TouchableOpacity style={styles.headerCloseIcon} onPress={this.props.onClose}>
            <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <rect x="5" y="17.9937" width="18" height="2" rx="1" transform="rotate(-45 5 17.9937)" fill="black"/>
            <rect x="6.5" y="5.49902" width="18" height="2" rx="1" transform="rotate(45 6.5 5.49902)" fill="black"/>
            </Svg>
          </TouchableOpacity>
        </View>
        <Ptext style={styles.text}>К сожалению, на мои услуги всегда очень большая очередь.</Ptext>
        <Ptext style={styles.mandatory}>Ближайшая дата записи ко мне: 12.12.2020</Ptext>
        <Ptext style={[styles.text,{marginBottom : 24}]}>Но у моей школы есть эксперты, которых обучала Я лично. Все они прошли экзаменацию и опытны в разборе матриц. Я гарантирую разбор высокого качества.</Ptext>
      </View>
    )
  }
}

const styles = StyleSheet.create({
  wholeForm : {
    flexDirection : 'column',
    width : '100%',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
    alignSelf : 'center',
    backgroundColor : '#ffffff',
  },
  header : {
    flexDirection : 'row',
    width : '100%',
    justifyContent : 'flex-start',
    alignItems : 'center',
    alignSelf : 'center',
    backgroundColor : '#ffffff',
  },
  headerTitle : {
    textAlign : 'left',
    marginTop : 8,
    marginLeft : 16,
    marginBottom : 4,
    fontSize : 18,
    fontWeight : 'bold',
    flex : 1,
  },
  headerCloseIcon : {

    alignSelf : 'flex-end',
    padding : 12
  },
  mandatory : {
    borderRadius : 4,
    backgroundColor : '#D0F1FF',
    padding : 12,
    alignSelf : 'center',
  },
  text : {
    textAlign : 'left',
    marginLeft : 16,
    marginTop : 6,
    marginBottom : 6,
  }
});


export default ServiceNotify;
