import React, { Component } from 'react';
import { View, StyleSheet,TouchableOpacity, TextInput } from 'react-native';
import Svg from 'react-native-svg';
import Ptext from "../../common/Ptext.js"

class BlogTopHeader extends Component {

  _changeText = (text) => {
    if (this.props.search)
      this.props.search(text);
  }


  render () {


    return (
        <View style={styles.wrapper}>
          <Ptext style={styles.caption}>Блог</Ptext>
          <TouchableOpacity style={styles.search}>
            <TextInput onChangeText={this._changeText}/>
            <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M9.5 3C11.2239 3 12.8772 3.68482 14.0962 4.90381C15.3152 6.12279 16 7.77609 16 9.5C16 11.11 15.41 12.59 14.44 13.73L14.71 14H15.5L20.5 19L19 20.5L14 15.5V14.71L13.73 14.44C12.59 15.41 11.11 16 9.5 16C7.77609 16 6.12279 15.3152 4.90381 14.0962C3.68482 12.8772 3 11.2239 3 9.5C3 7.77609 3.68482 6.12279 4.90381 4.90381C6.12279 3.68482 7.77609 3 9.5 3ZM9.5 5C7 5 5 7 5 9.5C5 12 7 14 9.5 14C12 14 14 12 14 9.5C14 7 12 5 9.5 5Z" fill="#B4BBC2"/>
            </Svg>
          </TouchableOpacity>
          <TouchableOpacity onPress={this.props.openBlogMenu} style={styles.icons}>
            <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="12" cy="6" r="2" fill="#B4BBC2"/>
            <circle cx="12" cy="12" r="2" fill="#B4BBC2"/>
            <circle cx="12" cy="18" r="2" fill="#B4BBC2"/>
            </Svg>
          </TouchableOpacity>

        </View>
    )
  }
}

const styles = StyleSheet.create({
  wrapper : {
    width : '90%',
    height : 32,
    marginTop : 13,
    marginBottom:13,
    alignItems : 'center',
    flexDirection : 'row'
  },
  search : {
    flex : 3,
    height : 24,
    justifyContent : 'flex-start',
    alignItems : 'center',
    flexDirection : 'row',
  },
  caption : {
    flex : 1,
    textAlign : 'left',
    fontSize : 24,
    lineHeight : 32,
    fontWeight : 'bold'
  },
  icons : {
    marginLeft : 13,
    marginRight : 13,

  }
});

export default BlogTopHeader;
