import React from 'react'
import {  StyleSheet, View, Text } from 'react-native'
import Svg, { Path, G, Mask } from 'react-native-svg'
import Ptext from "../../../common/Ptext.js"
import { NavLink } from '../../router/react-router';

export default function MatrixIcon({ style = {}, title = 'Matrix', title_visible = false, active = false, onPress = null, fontSize=14 })
{

  return (

    <NavLink to="/matrix" style={style}
        activeStyle={{
        fontWeight: "bold",
        color: '#1180AF'
      }}>

      <View style={styles.view}>
          <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
          <G>

            <Mask id="path-3-inside-1" fill="white">
            <Path d="M3 4C3 3.44772 3.44772 3 4 3H16C18.7614 3 21 5.23858 21 8V20C21 20.5523 20.5523 21 20 21H8C5.23858 21 3 18.7614 3 16V4Z"/>
            </Mask>
            <Path d="M3 4C3 3.44772 3.44772 3 4 3H16C18.7614 3 21 5.23858 21 8V20C21 20.5523 20.5523 21 20 21H8C5.23858 21 3 18.7614 3 16V4Z" stroke={active ? '#1180AF' : '#000'} strokeWidth="4" mask="url(#path-3-inside-1)"/>
            <rect x="9" y="4" width="1" height="15" fill={active ? '#1180AF' : '#000'}/>
            <rect x="14" y="4" width="1" height="15" fill={active ? '#1180AF' : '#000'}/>
            <rect x="4" y="10" width="1" height="15" transform="rotate(-90 4 10)" fill={active ? '#1180AF' : '#000'}/>
            <rect x="4" y="15" width="1" height="15" transform="rotate(-90 4 15)" fill={active ? '#1180AF' : '#000'}/>
          </G>
          </Svg>
          {title_visible ? <Text>{title}</Text> : null }
      </View>
    </NavLink>
  )
}



const styles = StyleSheet.create({
  icon: {
    height: 20,
    alignItems: 'center',
    backgroundColor : '#fff',
  },
  view:{
    flexDirection: 'row',
    alignItems: 'center',
    alignSelf : 'center',
    justifyContent : 'center',
  },
  text:{
    paddingLeft: 7,
    fontSize : 16,
  }
})
