import React, { Component } from 'react';
import { StyleSheet, TextInput , View } from 'react-native';
import { DatePicker,  } from '@kiwicom/universal-components'


class ItemDateInput extends Component {
  constructor(props) {
    super(props);
    this.state={};

  }

  componentDidMount() {
		// isValid method returns if the inputed value is valid.
		// Ex: if you input 40/02/1990 30:20:20, it will return false
		//	   because in this case, the day and the hour is invalid.

		// get converted value. Using type=datetime, it returns the moment object.
		// If it's using type=money, it returns a Number object.
	}

  render() {
  		// the type is required but options is required only for some specific types.
  		return (
        <View style={this.props.style}>
          <TextInput placeholder={'ДД.ММ.ГГГГ'} style={styles.textinput} onChangeText={this.props.onChangeText} value={this.props.bidValue}/>
          <DatePicker isVisible={false} labels={{ cancel : 'Отмена'}}>
          </DatePicker>
        </View>
      )
  	}
}

const styles = StyleSheet.create({
  textinput : {
    borderRadius : 4,
    borderColor : '#FFCB66',
    borderWidth : 1,
    textAlign : 'left',
    marginTop : 0,
    marginBottom : 0,
    paddingTop : 10,
    paddingBottom : 10,
    paddingLeft : 12,
    minWidth : 107,
    height : 40
  }
})

export default ItemDateInput;
