import React, { Component } from 'react';
import { View, StyleSheet } from 'react-native';
import ActionProfile from './actions/actionprofile.js'
import ActionNotify from './actions/actionnotify.js'
import ActionMessages from './actions/actionmessages.js'
import ActionOrders from './actions/actionorders.js'
import ActionSchool from './actions/actionschool.js'
import ActionSettings from './actions/actionsettings.js'
import ActionLogout from './actions/actionlogout.js'


class MenuContent extends Component {
  render () {
    return (
      <View style={[styles.actionsMenu,this.props.style]}>
          <ActionProfile eventsCnt={255}/>
          <ActionNotify eventsCnt={255}/>
          <ActionMessages eventsCnt={255}/>
          <ActionOrders eventsCnt={255}/>
          <ActionSchool eventsCnt={255}/>
          <ActionSettings eventsCnt={255}/>
          <ActionLogout eventsCnt={255} onPress={this.props.logOut}/>
          <View style={styles.actionLabel}/>
      </View>
    )
  }
}

const styles = StyleSheet.create({
  actionsMenu : {
    flexDirection : 'column',
    backgroundColor: '#fff',
    alignItems : 'flex-start',
    alignContent: 'flex-start',
    justifyContent : 'flex-start',
    width : '100%',
    flexGrow : 1
  },
  actionColumn : {
    width : '100%',
    height : 48,
    flexDirection : 'row'
  },
  actionColumnIcon : {
    width : 64,
    paddingLeft : 24,
    paddingTop : 12,
  },
  actionLabel : {
    flexGrow : 1
  },
})


export default MenuContent;
