import React,{Component} from 'react';
import {ScrollView,StyleSheet} from 'react-native';
import CommentsFull from './commentsFull.js';
import BlogPostStageHeader from '../blog/BlogPostStageHeader.js';


class CommentsPage extends Component {


  render () {
    let whole_width = '100%';
    if (this.props.scrollWidth)
      if (this.props.scrollWidth > 720 )
       whole_width = '95%';

    return(
      <ScrollView style={[styles.wrapper,{width:whole_width, height:this.props.t_height}]}
        contentContainerStyle={styles.container}>
        <BlogPostStageHeader stage={2} onBackPressed={this.props.closePage}/>
        <CommentsFull comments={this.props.comments}/>
      </ScrollView>
    )
  }
}

const styles = StyleSheet.create({
  wrapper : {
    flexDirection : 'column',
    alignSelf : 'center',
  },
  container : {
    justifyContent : 'flex-start',
    alignItems : 'flex-start'
  }

});

export default CommentsPage;
