import React from 'react'
import {View} from 'react-native'
import { NavLink } from '../../router/react-router';
import Ptext from "../../../common/Ptext.js"

export default function BlogIcon({ title = 'Blog', style = {} , active = false, onPress = null, fontSize = 14})
{
  return (
    <NavLink to="/blog" style={style}
        activeStyle={{
        fontWeight: "bold",
        color: '#1180AF'
      }}>

        {title}

    </NavLink>
  )
}
