import React, { Component } from 'react';
import { View, StyleSheet,TouchableOpacity } from 'react-native';
import Ptext from "../../../common/Ptext.js"
import Svg from 'react-native-svg'

class ActionMessages extends Component {
  render () {
      return (
        <TouchableOpacity onPress={this.props.onPress}>
          <View style={styles.actionColumn}>
            <View style={styles.actionColumnIcon}>
              <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M4.8 4H19.2C19.6774 4 20.1352 4.19889 20.4728 4.55293C20.8104 4.90696 21 5.38713 21 5.8878V17.2146C21 17.7153 20.8104 18.1955 20.4728 18.5495C20.1352 18.9035 19.6774 19.1024 19.2 19.1024H4.8C3.801 19.1024 3 18.2529 3 17.2146V5.8878C3 4.84007 3.801 4 4.8 4ZM12 10.6073L19.2 5.8878H4.8L12 10.6073ZM4.8 17.2146H19.2V8.12485L12 12.8349L4.8 8.12485V17.2146Z" fill="black"/>
              </Svg>
            </View>
            <View style={styles.actionLabel}>
              <Ptext style={styles.actionCaptionLabel}>
              Сообщения
              </Ptext>
              {this.props.eventsCnt !== 0 ?
              <View style={styles.actionEventsLabel}>
              <Ptext style={styles.actionEventsText}>
              {this.props.eventsCnt}
              </Ptext>
              </View> : null }
            </View>
          </View>
        </TouchableOpacity>
      )
    }
}

const styles = StyleSheet.create({
  actionColumn : {
    width : '100%',
    height : 48,
    flexDirection : 'row'
  },
  actionColumnIcon : {
    width : 64,
    paddingLeft : 24,
    paddingTop : 12,
  },
  actionLabel : {
    flex : 1,
    flexDirection : 'row',
    alignItems : 'center',
    justifyContent : 'flex-start',
    paddingLeft : 5
  },
  actionCaptionLabel : {
    fontSize : 16
  },
  actionEventsView : {
    flex : 1,
  },
  actionEventsText : {
    borderRadius : 10,
    borderWidth : 1,
    borderColor : '#45B4E3',
    color : '#45B4E3',
    borderStyle : 'solid',
    textAlign : 'left',
    paddingLeft : 8,
    paddingRight : 8,
    marginLeft : 8,
    marginRight : 8,
    fontSize : 16
  }
})
export default ActionMessages;
