import React, { Component } from 'react';
import { StyleSheet, TouchableOpacity, Image } from 'react-native';
import Ptext from "../../../common/Ptext.js"

class ContactChatItem extends Component {
  // default constructor
  _onPress = () => {
    if (this.props.onPressItem)
      this.props.onPressItem(this.props.id);
  }
  render () {
    return (
      <TouchableOpacity style={[styles.horizontalWrapper,{backgroundColor : '#fff'}]} onPress={this._onPress}>
        <Image style={styles.chatAvatar} source={this.props.chat.avatar_url}/>
        <Ptext style={styles.chatCaption}>{this.props.chat.chatName}</Ptext>
      </TouchableOpacity>
    )
  }

};

const styles = StyleSheet.create({
  horizontalWrapper : {
    width : '100%',
    alignSelf : 'center',
    justifyContent : 'flex-start',
    alignItems : 'center',
    flexDirection : 'row',
    height : 56,
  },
  chatAvatar : {
    width : 32,
    height : 32,
  },
  chatCaption : {
    fontSize : 16,
    lineHeight : 24,
    marginLeft : 17,
  },


});

export default ContactChatItem;
