import React, { Component } from 'react';
import { StyleSheet } from 'react-native';
import Ptext from "../../../common/Ptext.js"


class ItemHeaderLabel extends Component {
  constructor(props) {
    super(props);
    this.state={};

  }

  render() {
    return (
      <Ptext style={[styles.text,this.props.style]}>{this.props.title ? this.props.title : 'Введите дату рождения'}</Ptext>
    )
  }

}

const styles = StyleSheet.create({
  text : {
    textAlign : 'left',
  }

});

export default ItemHeaderLabel;
