import React,{Component} from 'react'
import {View,StyleSheet} from 'react-native'
import ProfileMenu from './menu/profileMenu.js'
import ProfileContent from './content/profileContent.js'


class ProfilePage extends Component {


  render (){
    return (
      <View style={styles.wrapper}>
        <ProfileMenu horizontal={true} match={this.props.match} history={this.props.history}/>
        <ProfileContent {...this.props} fullHeight={this.props.fullHeight-36-48}/>
      </View>
    )
  }


}

const styles = StyleSheet.create({
  wrapper : {
    flexDirection : 'column',
    width : '100%',
    alignSelf : 'center',
    alignItems : 'center',
    justifyContent : 'flex-start',
  }
});

export default ProfilePage;
