import React, { Component } from 'react';
import { TouchableOpacity, StyleSheet } from 'react-native';
import Ptext from './Ptext.js';

class Button extends Component {
  render () {
    return (
      <TouchableOpacity
                style={[styles.button,this.props.style]}
                onPress={this.props.onPress}
                disabled={this.props.disabled}
                underlayColor='#fff'>
                <Ptext style={[styles.title,this.props.styleTitle,{margin : 0,padding : 0}]}>{this.props.title}</Ptext>
       </TouchableOpacity>
    )
   }
};

const styles = StyleSheet.create({
  button : {
    marginTop : 12,
    marginBottom : 12,
    paddingTop: 12,
    paddingBottom : 12,
    backgroundColor : '#FFCB66',
    borderRadius : 4,
    borderWidth : 1,
    borderColor : '#FFCB66',
    alignItems : 'center',
    justifyContent : 'center',
  },
  title : {
    color : '#000',
    textAlign : 'center',
    paddingLeft : 12,
    paddingRight : 12,
    margin : 2,
  }
});

export default Button;
