class BlogListItem extends Component {


  showFullPost = () => {
    if (this.props.showFullPost)
      this.props.showFullPost(this.props.id);
    console.log('toFullPost button pressed');
  }

  render () {

    let comments = [
      {
        name : 'Мария Свердовская',
        text : 'Подскажите..А сектор талант он вообще какую то роль играет ?',
        time : '12 ч.',
        reply : [
          {
            name : 'Ольга Перцева',
            text : 'Конечно, главное правильно использовать исходные данные матрицы, и правильно определить переходы, чтобы максимально реализовать свои возможности',
            time : '10 ч.',
          },
          {
            name : 'Максим Евгенеевич',
            text : 'Очень правильное замечение, я заказал описание от эксперта и смог скорректировать свои изъяны и обратить их в преимущества',
            time : '8 ч.',
          },
        ]
      },
      {
        name : 'Мария Свердовская',
        text : 'Подскажите..А сектор талант он вообще какую то роль играет ?',
        time : '12 ч.',
        reply : [
          {
            name : 'Ольга Перцева',
            text : 'Конечно, главное правильно использовать исходные данные матрицы, и правильно определить переходы, чтобы максимально реализовать свои возможности',
            time : '10 ч.',
          },
          {
            name : 'Максим Евгенеевич',
            text : 'Очень правильное замечение, я заказал описание от эксперта и смог скорректировать свои изъяны и обратить их в преимущества',
            time : '8 ч.',
          },
        ]
      }
    ]
    var trimmedString = '';
    if (this.props.trim) {
      var yourString = this.props.post.body; //replace with your string.
      var maxLength = 400; // maximum number of characters to extract

      //trim the string to the maximum length
      trimmedString = yourString.substr(0, maxLength);

      //re-trim if we are in the middle of a word
      trimmedString = trimmedString.substr(0, Math.min(trimmedString.length, trimmedString.lastIndexOf(" ")))
      trimmedString += '...';
    }else {
      trimmedString = this.props.post.body;
    }
    if (this.props.scrollWidth<720)
    return (


      <View style={styles.wrapperVertical}>
        <ImageCarousel items={this.props.post.imgs} scrollWidth={this.props.scrollWidth}/>
        <View style={styles.contentWrapper}>
          <View style={styles.wrapperHorizontal}>
            <Ptext style={styles.title}>{this.props.post.title}</Ptext>
            <Ptext style={styles.time}>3 часа</Ptext>
          </View>
          <ItemActivityButtons/>

          <Ptext style={styles.liketitle}>Нравится: 144552</Ptext>
          <Ptext style={styles.post}>{trimmedString}</Ptext>
          {this.props.trim ?
          <Button title={'Читать далее'} style={styles.button} onPress={this.showFullPost}/>
          :
          null
          }
          <CommentsShort comments={comments} cnt={32} openComments={this.props.openComments}/>
        </View>
      </View>


    )
    else
    // !!!!!!!!!! WIDTH >= 720 ADAPTIVE
    return (
      <View style={[styles.wrapperHorizontal,{alignSelf : 'center',
        width : '95%',flexWrap : 'wrap','backgroundColor':'#fff'}]}>
        <View style={[styles.wrapperVertical,
            { alignItems : 'stretch',
              alignSelf : 'flex-start',
              justifyContent : 'flex-start',
              paddingTop : 24,
              width:'35%',
              paddingLeft : 12,
              shadowOpacity: 0,
            }]}>
          <ImageCarousel items={this.props.post.imgs} scrollWidth={216}/>
          <Ptext style={styles.liketitle}>Нравится: 144552</Ptext>
          <ItemActivityButtonsVert style={{flex : 1}} comment_cnt={32}/>
        </View>

        <View style={[styles.contentWrapper,
            { paddingTop : 24,flex : 1}]}>
          <Ptext style={styles.title}>{this.props.post.title}</Ptext>
          <Ptext style={styles.post}>{trimmedString}</Ptext>
          {this.props.trim ?
          <Button title={'Читать далее'} style={styles.button} onPress={this.showFullPost}/>
          :
          null
          }
          <TouchableOpacity style={{marginBottom : 16,flexDirection:'row',alignItems:'center'}}
            onPress={this.props.openComments}>
            <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M9 22C8.73478 22 8.48043 21.8946 8.29289 21.7071C8.10536 21.5196 8 21.2652 8 21V18H4C3.46957 18 2.96086 17.7893 2.58579 17.4142C2.21071 17.0391 2 16.5304 2 16V4C2 2.89 2.9 2 4 2H20C20.5304 2 21.0391 2.21071 21.4142 2.58579C21.7893 2.96086 22 3.46957 22 4V16C22 16.5304 21.7893 17.0391 21.4142 17.4142C21.0391 17.7893 20.5304 18 20 18H13.9L10.2 21.71C10 21.9 9.75 22 9.5 22H9ZM10 16V19.08L13.08 16H20V4H4V16H10Z" fill="#B4BBC2"/>
            </Svg>
            <Ptext style={{fontWeight:'bold'}}> Комментарии {32}</Ptext>
          </TouchableOpacity>

        </View>
        <View style={{width:'10%',
          height:'100%',
          backgroundColor :'#fff',
          flexDirection : 'row',
          paddingTop:24}}>
          <Ptext style={[styles.time]}>3 часа</Ptext>
        </View>
      </View>
    )
  }
}

const styles = StyleSheet.create({
  wrapperVertical : {
    flexDirection : 'column',
    alignSelf : 'center',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
    width : '100%',
    backgroundColor : '#fff',
    paddingTop : 0,
    borderBottomLeftRadius : 4,
    borderBottomRightRadius : 4,
    shadowOpacity: 0.2,
    shadowRadius: 2,
    shadowColor: '#000',
    marginBottom : 4,
    shadowOffset: { height: 1, width: 0 },
  },
  wrapperHorizontal : {
    flexDirection : 'row',
    alignSelf : 'center',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
    width : '100%',
    marginTop : 16,
  },
  contentWrapper : {
    flexDirection : 'column',
    alignSelf : 'center',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
    flex : 1,
    backgroundColor : '#fff',
    width:'95%',
  },
  title : {
    flex : 1,
    fontSize : 20,
    lineHeight : 24,
    textAlign : 'left',
    fontWeight : 'bold',
  },
  liketitle : {
    flex : 1,
    fontSize : 14,
    lineHeight : 24,
    textAlign : 'left',
    fontWeight : 'bold',
    marginTop : 16,
  },
  post : {
    flex : 1,
    fontSize : 14,
    lineHeight : 24,
    textAlign : 'left',
    marginTop : 16,

  },
  time : {
    textAlign : 'right',
    fontSize : 12,
    lineHeight : 16,
    color : '#B4BBC2'
  },
  button : {
    backgroundColor : '#fff',
    borderColor : '#b4bbc2',
    margin : 0,
    marginTop : 16,
    minWidth : 95,
    height : 32,
  }

})

export default BlogListItem;
