import React, { Component } from 'react';
import { StyleSheet,View, TextInput,TouchableOpacity } from 'react-native';
import Ptext from "../../../common/Ptext.js"
import PropTypes from 'prop-types';



class MessageInputNoAuth extends Component{

    render () {
    return (
        <View style={{minHeight : 48 + this.props.added_height,
               backgroundColor : '#fff',
               flexDirection : 'row',
               flex : 1,
               alignItems : 'center',
               width : '90%',
               flexWrap : 'wrap',
               justifyContent : 'flex-start'}}>
          <TouchableOpacity onPress={this.props.login}>
            <Ptext style={{color : '#45B4E3'}}>Для отправки сообщений войдите в систему</Ptext>
          </TouchableOpacity>

        </View>
      )
    }
}

MessageInputNoAuth.propTypes = {
  login : PropTypes.func.isRequired,
}

export default MessageInputNoAuth;
