import React, { Component } from 'react';
import { ScrollView,View, StyleSheet } from 'react-native';
import MenuHeader from "./menuheader.js"
import MenuStatistic from "./menustatistic.js"
import MenuContent from "./menucontent.js"

import { Dimensions } from 'react-native';


class SideMenu extends Component {
  state = {
    s_width : '100%',
  };
  constructor(props){
    super(props);

    let dims_w = Dimensions.get("screen");

    if (dims_w.width < 540)
      this.state.s_width = dims_w.width;
    else
      this.state.s_width = 320;

    this.closeMenuHandler = this.closeMenuHandler.bind(this);
  }

  dims_changed = dims => {
    let wd = '';
    if (dims.screen.width < 540){
        wd = dims.screen.width;
    }
    else {
        wd = 320;
    }
    this.setState({s_width : wd});
  }

  componentWillMount() {
        Dimensions.addEventListener("change", this.dims_changed);
        }

  componentWillUnmount() {
    // Important to stop updating state after unmount
      Dimensions.removeEventListener("change", this.dims_changed);

  }

  closeMenuHandler() {
    let cb = function cb() {
      this.props.cbCloseMenu();
    }.bind(this);
    setTimeout(cb,300);
  }


  render () {
    return (
      <ScrollView style={[styles.sidemenu,{width : this.state.s_width, height: window.innerHeight}]}>
        <View style={[styles.sidemenu,{width : '100%'}]}>
        <MenuHeader style={{width : this.state.s_width}} cbCloseMenu={this.closeMenuHandler}/>
        <MenuStatistic style={{width : this.state.s_width}}/>
        <MenuContent style={{width : this.state.s_width}}
          logOut={this.props.logOut}/>
        </View>
      </ScrollView>
    )
  }
}

const styles = StyleSheet.create({
  sidemenu :{
    right : 0,
    top : 0,
    bottom : 0,
    position : 'absolute',
    shadowColor: '#A4ACB3',
    shadowOffset: { height: 3, width: 0 },
    shadowOpacity: 0.5,
    shadowRadius: 5,
  },
  sidemenu_inner :{
    justifyContent: 'flex-start',
    alignItems : 'center',
    flexDirection : 'column',
  }
})

export default SideMenu;
