import React,{ Component } from 'react';
import { View, TouchableOpacity, StyleSheet } from 'react-native'
import Htext from "../../common/Htext.js"
import Ptext from "../../common/Ptext.js"
import Button from "../../common/Button.js"
import Svg from 'react-native-svg'

// TODO : add description showing in Animated View
/*
  Diplayed as
  <Header>
  <More> - pulldown textData
  <Price>
  <Button sign>


  input data  {
    service,
  }

*/

class MoreArrow extends Component {
  render () {
    return (
      <View >
        {this.props.active ?
          <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
          <rect x="17.4497" y="13.5" width="1" height="7" transform="rotate(135 17.4497 13.5)" fill="black"/>
          <rect x="13.207" y="9.25684" width="7" height="1" transform="rotate(135 13.207 9.25684)" fill="black"/>
          </Svg>
          :
          <Svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
          <rect x="6.55029" y="10.5" width="1" height="7" transform="rotate(-45 6.55029 10.5)" fill="black"/>
          <rect x="10.793" y="14.7432" width="7" height="1" transform="rotate(-45 10.793 14.7432)" fill="black"/>
          </Svg>
        }
      </View>
    )
  }
}


class ServiceItem extends Component {
  constructor(props){
    super(props);
    this.state = {
      showDescr : false
    };
  }

  showMore = () => {
    this.setState({showDescr : !this.state.showDescr});
  }
  onServiceGet = () => {
    if (this.props.onSelect)
      this.props.onSelect(this.props.id);
  }

  render () {
    return (
      <View style={[styles.vWrapper,this.props.style]}>
        <Htext style={styles.header}>{this.props.service.name}</Htext>
        <View
          style={{
          borderBottomColor: '#f0f1f3',
          borderBottomWidth: 1,
          paddingTop : 26,
          width : '100%',
        }}/>
        <TouchableOpacity onPress={this.showMore} style={[styles.hWrapper]}>
          <Ptext style={[styles.moreTitle]}>
            {this.props.moreTitle}
          </Ptext>
          <MoreArrow active={this.state.showDescr} onPress={this.showMore}/>
        </TouchableOpacity>
        {this.state.showDescr  ? <Ptext style={[styles.descr]}>{this.props.service.descr}</Ptext> : null}
        <View
          style={{
          borderBottomColor: '#f0f1f3',
          borderBottomWidth: 1,
          width : '100%',
        }}/>
        <Htext style={[styles.price,this.props.style]}>
          {this.props.service.price}{this.props.service.currency}
        </Htext>
        <Button title={'Записаться'} onPress={this.onServiceGet} style={styles.button}/>
      </View>
    )
  }
};

const styles = StyleSheet.create({
  vWrapper : {
    flexDirection : 'column',
    justifyContent : 'flex-start',
    alignItems : 'center',
    backgroundColor : '#fff',
    marginTop : 4,
    marginBottom : 4,
    padding : 0,
    boardRadius : 4,
    width : '90%',
    shadowColor: '#A4ACB3',
    shadowOffset: { height: 2, width: 1 },
    shadowOpacity: 0.5,
    shadowRadius: 2,
  },
  hWrapper : {
    flexDirection : 'row',
    justifyContent : 'flex-start',
    alignItems : 'center',
  },
  moreTitle : {

  },
  descr : {
    paddingTop : 20,
    paddingBottom : 20,
    textAlign : 'left',
  },
  price : {
    paddingTop : 15,
  },
  button : {
    minWidth : 137,
  },
  header : {
    paddingTop : 26,
    fontSize : 20
  }
});

export default ServiceItem;
