import React,{ Component } from 'react';
import { View, StyleSheet, TextInput } from 'react-native';
import ServiceFormPersonInput from "./serviceFormPersonInput.js"
import Ptext from "../../common/Ptext.js"
import Button from "../../common/Button.js"


class ServiceApplyForm extends Component {
  constructor(props){
    super(props);
    let xarr = [];

    for (let i = 0; i < props.count; i++)
      xarr.push({date : '',name : '', gender : 'male'});


    this.state = {
      count : props.form_num,
      form : xarr,
    };
  }

  dateEntered = (index,value) => {
    console.log("date i " + index + " v " + value);
    let ar = this.state.form;
    ar[index].date = value;
    this.setState({form : ar});
  }
  nameEntered = (index,value) => {
    console.log("i " + index + " v " + value);
    let ar = this.state.form;
    ar[index].name = value;
    this.setState({form : ar});
  }
  genderEntered = (index,value) => {
    console.log("gender i " + index + " v " + value);
    let ar = this.state.form;
    ar[index].gender = value;
    this.setState({form : ar});
  }
  commentChange = (value) => {
    this.setState({comment : value});
  }

  render () {
    return (
      <View style={styles.wholeForm}>
      {
        this.state.form.map((prop,key,arr) => {
          return (
            <View key={key} style={styles.oneItem}>
            <Ptext style={styles.caption}>Дата рождения</Ptext>
            <ServiceFormPersonInput
              dateEntered={this.dateEntered.bind(this,key)}
              genderEntered={this.genderEntered.bind(this,key)}
              nameEntered={this.nameEntered.bind(this,key)}/>
            </View>
          )
        })
      }
      <TextInput
          placeholder={'Комментарий к заказу'}
          onChangeText={this.commentChange}
          multiline={true}
          style={styles.comment}/>
      <Button style={styles.button}
        title={'Продолжить'}
        onPress={this.props.onNextStage}/>

      </View>

    )
  }
}

const styles = StyleSheet.create({
  wholeForm : {
    flexDirection : 'column',
    width : '100%',
    justifyContent : 'flex-start',
    alignItems : 'center',
    alignSelf : 'center',
    backgroundColor : '#ffffff',
  },
  caption : {
    textAlign : 'left',
    marginTop : 8,
    marginLeft : 16,
    marginBottom : 4,
    fontSize : 14,
  },
  oneItem : {
    flexDirection : 'column',
    width : '100%',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
  },
  comment : {
    width : '90%',
    borderRadius : 4,
    borderColor : '#B4BBC2',
    height : 120,
    borderWidth : 1,
    padding : 8,
    alignSelf : 'center'
  },
  button : {
    width : 208,
    marginRight : 16,
    alignSelf : 'flex-end',
    height : 40,
    borderWidth : 0,
  }

});


export default ServiceApplyForm;
