import React, { Component } from 'react';
import { View, StyleSheet } from 'react-native';
import MainIcon from "./icons/MainIcon.js";
import ProfileIcon from "./icons/ProfileIcon.js";
import BlogIcon from "./icons/BlogIcon.js";
import MatrixIcon from "./icons/MatrixIcon.js";
import ChatIcon from "./icons/ChatIcon.js";
import LoginIcon from "./icons/LoginIcon.js";
import SchoolIcon from "./icons/SchoolIcon.js";
import ServiceIcon from "./icons/ServiceIcon.js";
import MenuIcon from "./icons/MenuIcon.js";
import MessagesIcon from "./icons/MessagesIcon.js";
import NotifyIcon from "./icons/NotifyIcon.js";

import { Dimensions } from 'react-native';

class TopMenuComponent extends Component
{
    state = {
      dims : {width : -1}
    };
    constructor(props){
      super(props);

      this.dims_changed({screen : Dimensions.get("screen")});
    }


    //dims_changed = dims => console.log(dims," ",this);
    dims_changed = dims => {
      let res = this.props.supp.testRange(this.state.dims.width,dims.screen.width);
      if (res.neq) {
          let s = this.state;
          switch (res.range) {
            case 0:
                  s.mainWidth = '80%';
                  s.profileWidth = 24;
                  s.gambmenuWidth = '10%';
                  s.blogWidth = '20%';
                  s.serviceWidth = '20%';
                  s.matrixWidth = '20%';
                  s.chatWidth = '20%';
                  s.schoolWidth = '20%';
                  s.matrix_title_visible = false;
                  s.notify_visible = false;
                  s.messages_visible = false;
                  s.menu_height = 79;
                  s.text_size = 14;
                  s.text_main_size = 16;
              break;
            case 1:
                s.mainWidth = '22%';
                s.blogWidth = '13%';
                s.serviceWidth = '13%';
                s.matrixWidth = '13%';
                s.chatWidth = '13%';
                s.schoolWidth = '13%';
                s.profileWidth = 24;
                s.gambmenuWidth = '6.5%';
                s.matrix_title_visible = false;
                s.notify_visible = false;
                s.messages_visible = false;
                s.menu_height = 40;
                s.text_size = 14;
                s.text_main_size = 16;
              break;
              case 2:
                  s.mainWidth = '22%';
                  s.blogWidth = '13%';
                  s.serviceWidth = '13%';
                  s.matrixWidth = '13%';
                  s.chatWidth = '13%';
                  s.schoolWidth = '13%';
                  s.profileWidth = 32;
                  s.gambmenuWidth = '6.5%';
                  s.matrix_title_visible = false;
                  s.notify_visible = false;
                  s.messages_visible = false;
                  s.menu_height = 56;
                  s.text_size = 16;
                  s.text_main_size = 20;
                break;
                case 3:
                    s.mainWidth = '26%';
                    s.blogWidth = '12%';
                    s.serviceWidth = '12%';
                    s.matrixWidth = '12%';
                    s.chatWidth = '12%';
                    s.schoolWidth = '12%';
                    s.profileWidth = 32;
                    s.gambmenuWidth = '3.5%';
                    s.messagesWidth = '3.5%';
                    s.notifyWidth = '3.5%';
                    s.matrix_title_visible = true;
                    s.notify_visible = true;
                    s.messages_visible = true;
                    s.menu_height = 56;
                    s.text_size = 16;
                    s.text_main_size = 20;
                  break;
                  case 4:
                    s.mainWidth = '26%';
                    s.blogWidth = '12%';
                    s.serviceWidth = '12%';
                    s.matrixWidth = '12%';
                    s.chatWidth = '12%';
                    s.schoolWidth = '12%';
                    s.profileWidth = 32;
                    s.gambmenuWidth = '3.5%';
                    s.messagesWidth = '3.5%';
                    s.notifyWidth = '3.5%';
                    s.matrix_title_visible = true;
                    s.notify_visible = true;
                    s.messages_visible = true;
                    s.menu_height = 56;
                    s.text_size = 16;
                    s.text_main_size = 20;
                    break;
                    default:
                    s.mainWidth = '26%';
                    s.blogWidth = '12%';
                    s.serviceWidth = '12%';
                    s.matrixWidth = '12%';
                    s.chatWidth = '12%';
                    s.schoolWidth = '12%';
                    s.profileWidth = 32;
                    s.gambmenuWidth = '3.5%';
                    s.messagesWidth = '3.5%';
                    s.notifyWidth = '3.5%';
                    s.matrix_title_visible = true;
                    s.notify_visible = true;
                    s.messages_visible = true;
                    s.menu_height = 56;
                    s.text_size = 16;
                    s.text_main_size = 20;
                    break;
          }
          dims.screen.height = window.innerHeight;
          s.dims = dims.screen;
          this.setState(s);
          this.props.cbDimChanged(dims.screen.height - s.menu_height,dims.screen.width);
      }else {
        dims.screen.height = window.innerHeight;
        if (this.state.dims.height !== dims.screen.height)
          this.props.cbDimChanged(dims.screen.height - this.state.menu_height,dims.screen.width);
      }
    }

    _resize_window = (e) => {
      this.dims_changed({screen : Dimensions.get("screen")});
    }


    componentWillMount() {
          Dimensions.addEventListener("change", this.dims_changed);
          window.addEventListener('resize',this._resize_window);
    }

    componentWillUnmount() {
      // Important to stop updating state after unmount
        Dimensions.removeEventListener("change", this.dims_changed);
    }

    render () {
      return (
        <View style={[styles.menu,{height : this.state.menu_height}]}>
          <MainIcon title='Nenumerolog' style={{width: this.state.mainWidth,textDecoration : 'none'}} fontSize={this.state.text_main_size}/>
          {this.state.mainWidth === '80%' && this.props.auth?
              <ProfileIcon style={{width: this.state.profileWidth,
                                  height: this.state.profileWidth,textDecoration : 'none'}}/>
              : null }
            {this.state.mainWidth === '80%' && !this.props.auth?
                  <LoginIcon style={{width: this.state.profileWidth,
                                      height: this.state.profileWidth,textDecoration : 'none'}}
                              cbOnClick={this.props.toAuthPage}/>
                  : null }
          {this.state.mainWidth === '80%' && this.props.auth ? <MenuIcon style={{width:this.state.gambmenuWidth,textDecoration : 'none'}} onPress={this.props.cbOpenMenu}/> : null }
          <BlogIcon title='Блог'
              style={{width : this.state.blogWidth,textDecoration : 'none',color : '#000',textAlign : 'center',fontSize : this.state.text_size}} />
          <ServiceIcon title='Услуги'
            style={{width : this.state.serviceWidth,textDecoration : 'none',color : '#000',textAlign : 'center',fontSize : this.state.text_size}}/>
          <MatrixIcon title_visible={this.state.matrix_title_visible} title='Матрица'
            style={{width : this.state.matrixWidth,textDecoration : 'none',color : '#000',textAlign : 'center',fontSize : this.state.text_size}}/>
          <ChatIcon title='Чаты'
            style={{width : this.state.chatWidth,textDecoration : 'none',color : '#000',textAlign : 'center',fontSize : this.state.text_size}}/>

          <SchoolIcon title='Школа'
            style={{width : this.state.schoolWidth,textDecoration : 'none',color : '#000',textAlign : 'center',fontSize : this.state.text_size}}/>

          {this.state.mainWidth !== '80%' && this.props.auth ? <ProfileIcon style={{width: this.state.profileWidth,height: this.state.profileWidth,textDecoration : 'none'}}/>: null }
          {this.state.mainWidth !== '80%' && !this.props.auth ? <LoginIcon style={{width: this.state.profileWidth,height: this.state.profileWidth,textDecoration : 'none'}} cbOnClick={this.props.toAuthPage}/>: null }
          {this.state.notify_visible && this.props.auth ? <NotifyIcon style={{width: this.state.notifyWidth,textDecoration : 'none'}} notify={true} /> : null}
          {this.state.messages_visible && this.props.auth ? <MessagesIcon style={{width: this.state.messagesWidth,textDecoration : 'none'}} notify={true} /> : null}
          {this.state.mainWidth !== '80%' && this.props.auth ? <MenuIcon style={{width:this.state.gambmenuWidth,textDecoration : 'none'}} notify={true} onPress={this.props.cbOpenMenu}/> : null }
        </View>
      )
    }
/*


*/
}

const styles = StyleSheet.create({
  menu : {
    flexDirection : 'row',
    flexWrap : 'wrap',
    width: '100%',
    backgroundColor: '#fff',
    alignItems : 'center',
    justifyContent : 'space-between',
    shadowColor: '#A4ACB3',
    shadowOffset: { height: 1, width: 0 },
    shadowOpacity: 0.5,
    shadowRadius: 2,
    marginBottom : 2,
  },
  defaultIconStyle : {

  }

})
export default TopMenuComponent
